@import "../../styles/mixins";
@import "../../styles/variables";

.ak-video {
    width: 100%;
	display: flex;
	justify-content: center;
	color: $color-white;

	&__container {
		position: relative;
		box-shadow: toRem(0px) toRem(8px) toRem(32px) rgba(0, 0, 0, 0.16);
	}

	&__container::after {
		content: '';
        position: absolute;
        left: 0;
        bottom: toRem(-1px);
        right: 0;
        height: 25%;
		background-image: $gradient-from-bottom-to-top;
		border-radius: 0 0 toRem(5px) toRem(5px);
	}

	&__content {
		position: absolute;
		z-index: $z-index-1;
		bottom: toRem(24px);
		left: toRem(24px);
	}

	&__thumbnail {
		width: toRem(712px);
		height: toRem(400px);
		object-fit: cover;
		border-radius: toRem(5px);

		@media #{$breakpoint-tablet} {
			width: 100%;
			height: auto;
		}
	}

	&__btn-play {
		background-color: $color-blue-dodger;
		border-color: $color-blue-dodger;
		padding: toRem(8px);
		@media #{$breakpoint-tablet} {
			margin-bottom: toRem(16px);
		}
	}

	&__icon {
		width: toRem(20px);
		height: toRem(20px);
	}

	&__video {
		width: toRem(1000px);
		height: toRem(600px);

		@media #{$breakpoint-tablet} {
			width: toRem(600px);
			height:toRem(400px);
		}

		@media #{$breakpoint-mobile} {
			width: 100%;
			height: calc(200vw / 3); //to keep the aspect ratio 3:2
		}
	}

	&__show {
		transform:scale(1);
		//animation: pop-out 1.1s cubic-bezier(0.165, 0.840, 0.440, 1.000) forwards;
	}
}
