.ak-grid-row {
    width: 100%;

    &__content {
        width: 100%;
        display: flex;
        flex-direction: row;
        margin-bottom: 1.5rem;
        justify-content: space-between;

        @media #{$breakpoint-mobile} {
            margin-bottom: 0;
            flex-direction: column;
        }
    }

    &__columns {
        display: flex;
        width: 100%;
        flex-direction: row;
    }

    &__column {
        display: flex;
        height: 100%;
        flex-direction: column;

        @media #{$breakpoint-mobile} {
            margin-bottom: 1.5rem;
            height: auto;
        }
    }

    &__columns-two &__column {
        width: calc(50% - 0.75rem);

        @media #{$breakpoint-mobile} {
            width: 100%;
        }
    }

    &__columns-three &__column {
        width: calc(33.33% - 0.75rem);

        @media #{$breakpoint-mobile} {
            width: 100%;
        }
    }

    &--distribute-1-2-1 &__column {
        width: calc(50% - 1rem);

        &:first-child {
            width: calc(25% - 1rem);
            @media #{$breakpoint-mobile} {
                width: 100%;
            }
        }

        &:last-child {
            width: calc(25% - 1rem);
            @media #{$breakpoint-mobile} {
                width: 100%;
            }
        }

        @media #{$breakpoint-mobile} {
            width: 100%;
        }
    }

    &--align-top &__content {
        align-items: flex-start;
    }

    &--align-center &__content {
        align-items: center;
    }

    &--align-bottom &__content {
        align-items: flex-end;
    }

    &--align-stretch &__content {
        align-items: stretch;
    }

    &--gutters-default {
        padding: 0 1rem;
        @media #{$breakpoint-mobile} {
            padding: 0;
        }
    }
}

.cq-Editable-dom {
    .ak-grid-row__content {
        padding: 1rem;
    }
}
