@import "../../styles/mixins";
@import "../../styles/variables";

.ak-find-your-plan {
    width: 100%;

    &:not(.parbase) {
        padding: 3rem 1.5rem;
        border-radius: 0.5rem;
    }

    &--has-background {
        @include background-image();
    }

    &__title {
        margin-bottom: 0.5rem;

        @media #{$breakpoint-tablet-and-desktop} {
            text-align: center;
        }
    }

    &__description {
        margin-bottom: 1.5rem;

        @media #{$breakpoint-tablet-and-desktop} {
            text-align: center;
            margin-bottom: 2.5rem;
        }
    }
}
