@import "../mixins/utils";
@import "../../styles/variables";

@keyframes rotate-180 {
    100% {
        transform: rotate(-180deg);
    }
}

@keyframes fadein {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes fade-in-slide {
    0% {
        opacity: 0;
        transform: translateY(20px);
    }
    100% {
        opacity: 1;
        transform: translateY(0px);
    }
}

@keyframes zoom-in {
    0% {
        transform: scale(1.2);
    }
    100% {
        transform: scale(1);
    }
}

@keyframes openSearch {
    0% {
        opacity: 0;
        display: flex;
    }
    100% {
        opacity: 1;
    }
}

@keyframes pop-out {
	0% {
		transform:scale(0);
	}
	95.9% {
		transform: scale(1);
	}
}

@-webkit-keyframes drawer-content-open {
    0% {
        left: 120%;
    }

    100% {
        left: 50%;
    }
}

@keyframes drawer-content-open {
    0% {
        left: 120%;
    }

    100% {
        left: 50%;
    }
}

@-webkit-keyframes drawer-content-close {
    0% {
        left: 50%;
    }

    100% {
        left: 120%;
    }
}

@keyframes drawer-content-close {
    0% {
        left: 50%;
    }

    100% {
        left: 120%;
    }
}

@keyframes extra-plans {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

