@import "../../styles/mixins";
@import "../../styles/variables";

.ak-zip-code-form {
    max-width: 37.5rem;

    @media #{$breakpoint-tablet-and-desktop} {
        margin: 0 auto;
    }

    &__form-wrapper {
        position: relative;
        padding: toRem(3px);
        border-radius: toRem(4px);
        background-color: $color-white;
        border: solid 1px rgba(0, 0, 0, 0.08);
        box-shadow: 0 0 24px 0 rgba(16, 27, 83, 0.08);

        &--focus {
            border-color: $color-blue-klein;
        }

        &--hover {
            border-color: $color-blue-transparent;
        }
    }

    &__icon {
        top: 50%;
        left: 1rem;
        position: absolute;
        transform: translateY(-50%);
        pointer-events: none;
    }

    &__input-text {
        flex: 1;
        border: none;
        padding-left: 1rem;

        &:focus {
            box-shadow: none;
        }

        @media #{$breakpoint-tablet-and-desktop} {
            padding-left: 2.75rem;
        }
    }

    &__custom-border {
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        position: absolute;
        background-color: $color-white;
    }

    &__submit-button {
        @media #{$breakpoint-mobile} {
            min-width: 0;
            padding-left: 0.875rem;
            padding-right: 0.875rem;
        }
    }

    &__error-message {
        color: $color-red;
        margin-top: 0.5rem;
        padding-left: 0.5rem;
    }
}
