@import "../variables";
@import "../mixins/typography";

@mixin base-input() {
    @include body-3();
    min-width: 0;
    padding: 1rem;
    border-radius: 0.25rem;
    border: 1px solid $color-gray-iron;
    font-family: $font-family-secondary;

    &:hover {
        border-color: $color-blue-transparent;
    }

    &:focus {
        outline: none;
        border-color: $color-blue-solid;
    }

    &:disabled {
        background-color: $color-gray-light;
        cursor: not-allowed;
    }

    &::-webkit-input-placeholder {
        color: $color-gray-french;
    }

    &:-moz-placeholder {
        color: $color-gray-french;
        opacity: 1;
    }

    &::-moz-placeholder {
        color: $color-gray-french;
        opacity: 1;
    }

    &:-ms-input-placeholder {
        color: $color-gray-french;
    }

    &::placeholder {
        color: $color-gray-french;
    }
}


@mixin search-input {
    background-color: transparent;
    border: none;
    font-family: $font-family-secondary;
    color: $color-black-woodsmoke;
    padding: 1rem 0;
    border-radius: 4px;

    &:hover {
        color: $color-gray-corduroy;
    }

    &:focus {
        box-shadow: none;
        border: 1px solid $color-blue-solid;
    }


    &::-webkit-input-placeholder {
        color: $color-gray-french;
    }

    &:-moz-placeholder {
        color: $color-gray-french;
        opacity: 1;
    }

    &::-moz-placeholder {
        color: $color-gray-french;
        opacity: 1;
    }

    &:-ms-input-placeholder {
        color: $color-gray-french;
    }

    &::placeholder {
        color: $color-gray-french;
    }
}
