@import '../../styles/mixins';
@import '../../styles/variables';

.ak-offer {
    &:not(.parbase) {
        width: 100%;
        margin-bottom: 2rem;
        max-width: toRem(327px);

        @media #{$breakpoint-tablet-and-desktop} {
            max-width: none;
            width: calc((100% - 3rem) / 3);
        }
    }

    &__wrapper {
        height: 100%;
        display: flex;
        overflow: hidden;
        border-radius: 3px;
        flex-direction: column;
        background-color: $color-white;

        @media #{$breakpoint-mobile} {
            margin: 0 0 toRem(24px);
        }
    }

    &__wrapper--animate {
        animation: extra-plans 1s ease-out 1 forwards;
    }

    &__header {
        width: 100%;
        display: flex;
        flex-direction: row;
        height: toRem(40px);
        padding: toRem(12px);
        justify-content: center;
        background-color: $color-gray-athens;

        .base-text--type-sub {
            color: $color-blue-klein;
        }

        &--blue {
            background-color: $color-blue-dodger;

            .base-text--type-sub {
                color: $color-white;
            }
        }
    }

    &__title {
        display: flex;
        align-items: center;
        flex-direction: row;
        color: $color-blue-klein;
    }

    &__title-icon {
        width: 1.5rem;
        height: 1.5rem;
        margin-left: toRem(10px);
    }

    &__duration {
        color: $color-gray-corduroy;
    }

    &__green-wrapper {
        width: 100%;
        display: flex;
        align-items: center;
        margin-bottom: 1.5rem;
        justify-content: flex-start;
    }

    &__green-text {
        margin-left: 0.5rem;
    }

    &__green-image {
        width: toRem(70px);
        height: toRem(70px);
    }

    &__pricing {
        width: 100%;
        display: flex;
        min-width: 1px;
        max-width: toRem(86px);
        flex-direction: column;
        align-items: flex-start;
        color: $color-gray-corduroy;

        .highlight {
            @include heading-h3();
            color: $color-black-woodsmoke;
        }
    }

    &__description {
        width: 100%;
        flex: 1 1 auto;
        position: relative;
        padding-left: toRem(33px);
        color: $color-gray-corduroy;

        &:before {
            top: 50%;
            left: 1rem;
            width: 1px;
            content: '';
            height: 100%;
            position: absolute;
            transform: translate(-50%, -50%);
            background-color: $color-gray-corduroy;
        }
    }

    &__content {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        padding: toRem(24px);
        flex-direction: column;
        justify-content: space-between;

        &-header {
            width: 100%;
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            margin-bottom: toRem(40px);

            .base-icon {
                $icon-size: toRem(20px);

                width: $icon-size;
                height: $icon-size;
                margin-left: toRem(10px);
            }
        }

        &-description-wrapper {
            display: flex;
            flex-direction: row;
            align-items: center;
            margin-bottom: toRem(40px);
        }

        &-cta {
            width: 100%;
            margin-top: auto;
            max-width: unset;
        }
    }

    &__description-block {
        display: flex;
        flex-wrap: wrap;
    }

    &__offer-type-info {
        display: flex;
        width: 100%;
        padding-left: toRem(33px);
        margin-bottom: toRem(6px);
    }

    &__offer-type {
        color: $color-black-woodsmoke;
    }

    &__offer-type-icon {
        height: 1rem;
        padding-left: toRem(7px);
    }

    &__footer {
        display: flex;
        justify-content: center;
    }

    &__footer-cta {
        display: flex;
        align-items: center;
        flex-direction: row;
        justify-content: center;
        margin-bottom: toRem(24px);
        color: $color-gray-corduroy;

        .base-icon {
            $icon-size: 1rem;
            width: $icon-size;
            height: $icon-size;
            margin-left: 0.5rem;

            & path {
                stroke: $color-gray-corduroy;
            }
        }
    }
}

// Styles for the offer when it's inside the offer-container and not render dynamically like on the Plans Frame
.ak-offer-container {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;

    @media #{$breakpoint-mobile} {
        align-items: center;
        flex-direction: column;
    }

    //Offer parbase width inside the offer container
    .ak-offer.parbase {
        width: 100%;
        margin-bottom: 2rem;
        max-width: toRem(327px);

        @media #{$breakpoint-tablet-and-desktop} {
            max-width: none;
            width: calc((100% - 3rem) / 3);

            &:nth-child(3n + 2) {
                margin-left: 1.5rem;
                margin-right: 1.5rem;
            }
        }
    }

    .ak-offer:not(parbase) {
        width: 100%;
        margin-bottom: 0;
    }

    ak-offer {
        height: 100%;
        display: flex;
    }

    //For Mass Market offer styling to make them look stacked
    &--display-col {
        flex-direction: column;

        .ak-offer.parbase {
            margin: 2rem;
        }

        .ak-offer:not(parbase) {
            margin-bottom: 0;
        }

        .newpar {
            width: 90%;
            margin: auto;
        }
    }
}
