@import "../../styles/mixins";
@import "../../styles/variables";

.ak-back-button {
    color: $color-white;
    background-color: $color-primary;

    &__container {
        padding: 1rem 0;
        @include base-container();
    }

    &__link {
        display: inline-block;
    }

    &__arrow {
        width: toRem(18px);
        margin-right: toRem(10px);;
        transform: rotateY(180deg);
    }

    &__text {
        font-family: $font-family-primary;
    }
}
