@import "../../styles/mixins";
@import "../../styles/variables";

.ak-logo-plus-more-info {

    &--border-top {
        border-top: 1px solid $color-gray-iron;
    }

    &--border-bottom {
        border-bottom: 1px solid $color-gray-iron;
    }

    &__container {
        display: flex;
        margin-top: 1rem;
        margin-bottom: 1rem;
        justify-content: space-between;
        @include base-container();

        @media #{$breakpoint-tablet} {
            flex-direction: column;
            align-items: flex-start;
        }
    }

	&__right-panel {
		margin-left: 1rem;
	}

    &__image {
        max-width: 4rem;
		max-height: 4rem;
		margin-right: 1rem;
		@media #{$breakpoint-tablet} {
			margin-right: 0;
			margin-bottom: 1rem;
		}
	}

	&__title {
		word-break: break-word;
		@media #{$breakpoint-tablet} {
			margin-bottom: 0.5rem;
		}
	}

	&__description {
		word-break: break-word;
		margin-right:  1rem;
		width: 100%;
	}

	&__button {
		@media #{$breakpoint-tablet} {
			margin-top: 1.5rem;
		}
	}
}
