@import "../../../styles/variables";

.base-checkbox {

    display: flex;
    position: relative;
    flex-direction: row;
    align-items: flex-start;

    &__input {
        position: absolute;
        opacity: 0;
        cursor: pointer;
        height: 100%;
    }

    &__selected {
        display: none;
        transition: all 0.5s ease;
    }

    &__input:checked ~ &__icon-container &__selected {
        display: block;
    }

    &__input:checked ~ &__icon-container &__unselected {
        display: none;
        transition: all 0.5s ease;
    }

    &__icon-container {
        width: 2.5rem;
        height: 2.5rem;
        position: relative;
        margin-right: .5rem;
        border-radius: 50%;
        margin-left: -.75rem;
        cursor: pointer;
    }

    &__icon-container--darker {
        border: 2px solid transparent;
    }

    &__icon-container:hover {
        background-color: $color-gray-light;
    }

    &__input:focus ~ &__icon-container {
        background-color: $color-white-lilac;
    }

    &__icon-container--darker:hover {
        background-color: $color-gray-athens
    }

    &__input:focus ~ &__icon-container--darker {
        background-color: transparent;
        border: 2px solid $color-blue-dodger;
    }

    &__icon {
        position: absolute;
        width: 1.25rem;
        height: 1.25rem;
        top: .6rem;
        left: .7rem;
    }

    &__icon-container--darker &__icon {
        top: .5rem;
        left: .5rem;
    }

    &__text {
        margin-top: 0.5rem;
        max-width: calc(100% - 2.5rem);
    }
}
