@import "../../styles/mixins";
@import "../../styles/variables";

.ak-community-frame {

    &__header {
        width: 100%;
        border-bottom: 1px solid $color-gray-iron;
    }

    &__header-container {
        @include base-container();
        $vertical-padding: toRem(48px);

        display: flex;
        flex-direction: row;
        justify-content: space-between;
        padding-top: $vertical-padding;
        padding-bottom: $vertical-padding;

        @media #{$breakpoint-tablet} {
            $vertical-padding: toRem(24px);

            flex-direction: column-reverse;
            padding-top: $vertical-padding;
            padding-bottom: $vertical-padding;
        }

        &__left,
        &__right {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
        }

        &__right {
            align-items: flex-end;
            justify-content: flex-end;
            max-width: toRem(175px);
            @media #{$breakpoint-tablet} {
                $vertical-padding: toRem(24px);

                max-width: unset;
                flex-direction: row;
                align-items: center;
                justify-content: space-between;
                margin-bottom: $vertical-padding;
                padding-bottom: $vertical-padding;

            }
        }
    }

    &__phone-link {
        display: flex;
        padding: .5rem;
        margin-top: .5rem;
        align-items: center;
        flex-direction: row;
    }

    &__phone-link-icon {
        margin-right: .5rem;
    }
}
