@import "../../styles/variables";

.ak-space {

    &--small {
        height: .5rem;
    }

    &--medium {
        height: 1rem;
    }

    &--large {
        height: 2rem;
    }

    &--extra-large {
        height: 4rem;
    }

    &--jumbo {
        height: 7rem;
    }

    &--edit {
        background-color: $color-blue-transparent;
    }
}
