.ak-tabs-navigation {
    width: 100%;
    min-height: 4rem;

    &--edit {
        padding: 2rem;
    }

   &__header-container {
       position: relative;
   }

    &__header {
        display: flex;
        width: 100%;
        border-top: 1px solid $color-gray-iron;
        justify-content: center;
        white-space: nowrap;
        flex-wrap: nowrap;
        height: 4.6rem;
        overflow: auto;

        @media #{$breakpoint-mobile} {
            justify-content: left;
            padding: 0 1.5rem;
        }
    }

    &__line {
        height: 1px;
        background-color: $color-gray-iron;
        position: absolute;
        width: 100%;
        top: 4.6rem;
    }

    &__tab-button {
        padding: 1.5rem 2.5rem;
        color: $color-gray-corduroy;
        position: relative;
        outline: none;
        font-weight: 600;
        transition: all 0.3s ease-in-out;
        height: 100%;
        @include body-3();

        &:hover {
            background-color: $color-gray-light;
            color: $color-black-woodsmoke;
        }

        &:focus {
            background-color: $color-white-lilac;
            color: $color-blue-klein;
        }

        &:before {
            content: '';
            position: absolute;
            bottom: 0;
            height: 1px;
            width: 0;
            background-color: transparent;
            transition: all 0.3s ease-in-out;
        }

        @media #{$breakpoint-mobile} {
            padding: 1.5rem 1rem;
        }
    }

    &__tab-button--active {
        color: $color-blue-klein;

        &:before {
            width: calc(100% - 5rem);
            background-color: $color-blue-klein;
        }

        @media #{$breakpoint-mobile} {
            &:before {
                width: calc(100% - 2rem);
            }
        }
    }

    &__section-label {
        color: $color-gray-iron;
    }
}
