/**** FONTS ****/

$font-size-base: 16px;
$font-family-primary: 'Gotham-Medium', sans-serif;
$font-family-secondary: 'Gotham-Book', sans-serif;

/**** Z-INDEX, CONTAINERS ****/
$z-index-skip-link: 500;
$z-index-header: 300;
$z-index-header-open: 350;
$z-index-header-overlay: 250;
$z-index-modal: 300;
$z-index-modal-overlay: 300;
$z-index-modal-elements: 325;
$z-index-modal-top-elements: 350;
$z-index-top-element: 250;

$container-max-size: 67.5rem;
$container-skinny-max-size: 44.5rem;
$container-regular-padding: 1.5rem;
$container-mobile-padding: 1rem;
$z-index-10: 10;
$z-index-1: 1;

$header-height: toRem(75px);
$enrollment-header-height: toRem(72px);
$enrollment-header-mobile-height: toRem(137px);

/**** COLORS ****/

// Definitions

$color-blue-klein: #0032a0;
$color-blue-picton: #71c5e8;
$color-blue-navy: #000c84;
$color-blue-darker-navy: #000a69;
$color-blue-dodger: #009cde;
$color-purple-heart: #721edc;
$color-black-woodsmoke: #101012;
$color-gray-corduroy: #6f7271;
$color-gray-french: #babbc5;
$color-gray-athens: #dee1ea;
$color-gray-athens-transparent: rgba(222, 225, 229, 0.5);
$color-gray-transparent: rgba(222, 225, 229, 0.64);
$color-gray-iron: #dee1e5;
$color-gray-light: #f8f8fa;
$color-blue-polar: #eff8fc;
$color-white: #ffffff;
$color-white-darker: #eff1f5;
$color-red: #dc0000;
$color-red-light: #fdeeee;
$color-white-alabaster: #f9f9f9;
$color-transparent-white: rgba(255, 255, 255, 0.5);
$color-blue-solid: rgba(0, 15, 159, 1);
$color-blue-transparent: rgba(0, 15, 159, 0.5);
$color-light-blue-transparent: rgba(0, 15, 159, 0.16);
$color-black-transparent: rgba(17, 20, 34, 0.6);
$color-black-overlay-transparent: rgba(0, 0, 0, 0.8);
$color-gray-light-transparent: rgba(248, 248, 250, 0.5);
$color-white-lilac: #f5f5fb;
$color-blue-header: #376cf6;

// Usages

$color-primary: $color-blue-klein;
$color-secondary: $color-blue-picton;
$color-accent: $color-purple-heart;
$color-copy: $color-black-woodsmoke;
$color-footer-separator: $color-white-alabaster;
$color-focus-inputs: $color-blue-dodger;

$gradient-from-bottom-to-top: linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.5));
$image-overlay-gradient: linear-gradient(
    $color-black-overlay-transparent,
    $color-light-blue-transparent
);

/**** BREAKPOINTS ****/

$mobile-xs: 450px;
$tablet-min: 768px;
$desktop-min: 1024px;
$mobile-max: ($tablet-min - 1);
$tablet-max: ($desktop-min - 1);
$desktop-lg: 1200px;
$desktop-xl: 1800px;

/**** MEDIA QUERIES ****/

$breakpoint-mobile-xs: 'screen and (max-width: #{$mobile-xs})';
$breakpoint-mobile: 'screen and (max-width: #{$mobile-max})';
$breakpoint-tablet: 'screen and (max-width: #{$tablet-max})';
$breakpoint-desktop-only: 'screen and (min-width: #{$desktop-min})';
$breakpoint-desktop-lg-only: 'screen and (min-width: #{$desktop-lg})';
$breakpoint-desktop-xl-only: 'screen and (min-width: #{$desktop-xl})';
$breakpoint-tablet-and-desktop: 'screen and (min-width: #{$tablet-min})';
