@import "../../../styles/mixins";
@import "../../../styles/variables";

.base-text-area {

    display: flex;
    flex-direction: column;

    &__label {
        @include body-4();
        margin-bottom: .5rem;
    }

    &__input {
        @include base-input();
        height: 10rem;
        resize: none;
    }

    &__input--invalid {
        border-color: $color-red;
    }
}
