@import "../../styles/mixins";
@import "../../styles/variables";

.ak-enrollment-header {

    height: $enrollment-header-height;
    background-color: $color-primary;
    color: $color-white;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: row;
    width: 100%;
    z-index: $z-index-header;

    @media #{$breakpoint-mobile} {
        height: $enrollment-header-mobile-height;
    }

    &__container {
        @include base-container();
        height: 100%;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;

        @media #{$breakpoint-mobile} {
            flex-direction: column;
            align-items: flex-start;
            padding-top: 1rem;
            padding-bottom: 1rem;
        }
    }

    &__nav-icon {
        width: 7rem;
        @media #{$breakpoint-mobile} {
            width: 6rem;
        }
    }

    &__content {
        display: flex;
        flex-direction: row;
        align-items: center;

        @media #{$breakpoint-mobile} {
            margin-top: 1rem;
        }
    }

    &__label {
        padding-right: 1rem;
        @media #{$breakpoint-mobile} {
            padding-right: 1.5rem;
        }
    }

    &__phone-container {
        align-items: center;
        border-radius: 4px;
        padding: .5rem;
        border: solid 1px $color-white
    }

    &__phone-icon {
        width: 1.1rem;
        margin-right: .5rem;
    }
}

