@import "../../../styles/mixins";
@import "../../../styles/variables";

.base-button {
    @include base-button();

    cursor: pointer;
    position: relative;
    text-decoration: none;

    .loading {
        width: 1.5rem;
        height: 1.5rem;
        display: block;
        margin: 0 auto;
        @include background-image();
        background-size: contain;
        background-image: url('/etc.clientlibs/settings/wcm/designs/akron/clientlib-all/resources/images/blue-spinner.gif');
    }

    &--primary {
        color: $color-white;
        background-color: $color-primary;

        &:hover {
            background-color: $color-blue-navy;
        }

        &:active {
            background-color: $color-blue-darker-navy;
        }

        .loading {
            background-image: url('/etc.clientlibs/settings/wcm/designs/akron/clientlib-all/resources/images/white-spinner.gif');
        }
    }

    &--secondary {
        color: $color-primary;
        background-color: $color-white;

        &:hover {
            background-color: $color-white-darker
        }

        &:active {
            background-color: $color-gray-athens;
        }
    }

    &--tertiary {
        color: $color-primary;
        border-color: $color-gray-light;
        background-color: $color-gray-light;

        &:hover {
            border-color: $color-white-darker;
            background-color: $color-white-darker;
        }

        &:active {
            border-color: $color-gray-athens;
            background-color: $color-gray-athens;
        }
    }

    &--pagination {
        @include cta-text();
        border: none;
    }

    &--fluid {
        width: 100%;
    }

    &--disabled {
        pointer-events: none;
	}

	&--icon {
		min-width: unset;
    	padding: toRem(10px);
	}

	&__align {
		display: flex;

		&--center {
			justify-content: center;
		}
	}

}

.ak-button:not(.parbase) {
	width: 100%;
}
