@import "../../styles/variables";

.ak-message {

    &__container {
        text-align: center;
        width: 66%;
        margin: 0 auto;

        @media #{$breakpoint-mobile} {
            width: 100%;
        }
        
        &--publish {
            padding-top: 10rem;
            min-height: calc(100vh - #{$header-height});
        }
    }

    &__icon {
        width: 2.5rem;
        height: 2.5rem;
        object-fit: cover;
    }

    &__title {
        margin-top: 1.5rem;
    }

    &__description {
        margin-top: .5rem;
        color: $color-gray-corduroy;
    }

    &__button {
        margin-top: 2.5rem;
    }
}
