@import "../../styles/mixins";
@import "../../styles/variables";

.ak-investors-relations {
    &__content {
		@include base-container();
		margin-top: toRem(64px);
        display: flex;
        flex-direction: column;
        width: 100%;
        justify-content: center;
		align-items: center;
		border-radius: toRem(3px);
	}

	&__disclaimer {
		@include base-container();
		position: relative;
		background-color: $color-gray-light;
		width: 100%;
		padding: toRem(48px) toRem(184px);
		margin-bottom: toRem(64px);
	}

	&__subtitle {
		color: $color-gray-corduroy;
	}

	&__title {
		margin-bottom: toRem(20px);
	}

	&__description {
		margin-bottom: toRem(40px);
	}

	&__children-content {
		@include base-container();
		position: relative;
	}

	&__opacity {
		opacity: 0.5;
	}

	&__overlay {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: $z-index-1;
	}
}
