@import '~swiper/swiper';
@import "../../styles/mixins";

.ak-testimonial {
    min-height: 4rem;
    max-width: 100%;
    min-width: 100%;
    position: relative;

    &__hidden {
        display: none;
    }

    &__container {
        padding: 1rem 0 2rem;
        position: relative;
    }

    &__pagination {
        display: flex;
        justify-content: center;
        margin-bottom: -1rem;
    }

    &__testimonial {
        margin: 4.5rem 0 0 0;
        width: calc(100% - 22.5rem);
    }

    &__quotation {
        position: absolute;
        top: 2rem;
        left: 8.5rem;
        width: 6.5rem;
        height: 5.3rem;
    }

    &__user {
        margin: 4rem 0 0 0;
        display: flex;
        align-items: center;
    }

    &__image-container {
        width: 3rem;
        height: 3rem;
    }

    &__image {
        width: 100%;
        height: 100%;
        border-radius: 100%;
        object-fit: cover;
    }

    &__text {
        margin-left: 1rem;
    }

    &__name {
        font-size: toRem(24px);
        font-weight: 600;
    }

    &__button-prev, &__button-next {
        width: 3.25rem;
        min-width: 3.25rem;
        height: 3.25rem;
        border-radius: 100%;
        background: $color-white;
        position: absolute;
        top: 50%;
        z-index: $z-index-10;
        padding: 0;
    }

    &__button-prev {
        left: 0;
    }

    &__button-next {
        right: 0;
    }

    &__icon {
        margin: 0 auto;
    }

    .swiper-wrapper {
        width: calc(100% - 22.5rem);
        margin: 0 auto;
    }

    .swiper-pagination-bullet {
        width: .5rem;
        height: .5rem;
        display: inline-block;
        border-radius: 100%;
        background: $color-blue-dodger;
        opacity: 0.25;
        margin: toRem(4px) toRem(12px);
        cursor: pointer;

        &:hover {
            opacity: 1
        }

        &:focus {
            margin: 0 .5rem;
            width: 1rem;
            height: 1rem;
            border: toRem(4px) solid $color-gray-athens;
            opacity: 1;
            outline: none;
        }
    }

    .swiper-pagination-bullet-active {
        margin: 0 .5rem;
        width: 1rem;
        height: 1rem;
        border: toRem(4px) solid $color-gray-athens;
        opacity: 1;
    }

    .swiper-slide {
        visibility: hidden;
    }

    .swiper-slide-active {
        visibility: visible;
    }

    @media #{$breakpoint-mobile} {
        &__container {
            padding: 2rem 0 5rem 0;
            position: relative;
        }

        &__quotation {
            width: 4.4rem;
            height: 3.4rem;
            top: -.5rem;
            left: -0.8rem;
        }

        &__testimonial {
            margin: 0;
            width: 100%
        }

        &__button-prev {
            right: 4.6rem;
            bottom: 1rem;
            left: unset;
            top: unset;
        }

        &__button-next {
            right: 0;
            bottom: 1rem;
            left: unset;
            top: unset;
        }

        .swiper-pagination-bullet {
            visibility: hidden;
            display: none;
        }

        .swiper-container {
            position: unset;
        }

        .swiper-wrapper {
            width: 100%;
            margin: auto;
        }
    }
}

