@import "../../styles/mixins";
@import "../../styles/variables";

.ak-request-a-quote {
    &__container {
        width: 100%;
        display: flex;
        flex-direction: row;
	}

	&__fieldset{
		margin-top: 4rem;

		&:first-child {
			margin-top: 0;
		}
	}

	&__title {
		margin-bottom: 2.5rem;
	}

    //Form
    &__form {
        display: flex;
        flex-direction: column;
        width: 70%;
        margin: 0 -.75rem;
		padding-right: 1.8rem;

        @media #{$breakpoint-mobile} {
            width: 100%;
            padding: 0;
            margin: 0;
        }
    }

    &__row {
        display: flex;
        flex-direction: row;
        width: 100%;
        margin-top: 1.7rem;

        @media #{$breakpoint-mobile} {
            flex-direction: column;
            width: 100%;
            margin-top: 0
        }
    }

    &__row--aligned-right {
        justify-content: flex-end;

        @media #{$breakpoint-mobile} {
            flex-direction: row;
            width: auto;
            margin-top: 2.5rem;
        }
    }

    &__subtitle {
        color: $color-gray-corduroy;
    }

    &__input {
        padding: 0 .75rem;
        display: flex;
        flex-direction: column;
        width: 50%;
        position: relative;

        @media #{$breakpoint-mobile} {
            width: 100%;
            padding: 0;
            margin-top: 1.8rem;
        }
    }

    &__input--full {
        width: 100%;

        @media #{$breakpoint-mobile} {
            margin-top: 1.8rem;
        }
    }

    &__radio-button-container {
        display: flex;
        flex-direction: row;
        width: 100%;

        @media #{$breakpoint-mobile} {
            flex-direction: column;
        }
    }

    &__error-message {
        color: $color-red;
        position: absolute;
        bottom: -1.5rem;
    }

    &__invalid {
		bottom: 1.5rem;
		padding: 1rem;
		color: $color-red;
		text-align: right;
    }

    &__submit {
        margin-right: 1rem;
    }

    //Right Panel
    &__right-panel {
        padding-left: 1.8rem;
		width: 30%;
		margin-top: toRem(100px);

        @media #{$breakpoint-mobile} {
            display: none;
        }
	}

	&__disclaimer {
		background-color: $color-gray-light;
		padding: .5rem 2rem;
		display: flex;
		align-items: center;
		justify-content: center;
		border-radius: toRem(4px);
		margin-bottom: 2.5rem;

		@media #{$breakpoint-mobile} {
            align-items: baseline;
        }

		&--image {
			width: toRem(13px);
			height: toRem(13px);
			margin-right: toRem(10px);
		}
	}

    &__captcha {
        @media #{$breakpoint-mobile} {
            .ak-request-a-quote__error-message {
                width: 100%;
                text-align: center;
            }

            & > div > div > div {
                margin: 0 auto;
            }
        }
    }
}
