@import '../../styles/mixins';
@import '../../styles/variables';

.offer-details {
    &__content {
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }

    &__information {
        padding: 0 4.8rem;
        overflow-y: auto;

        @media #{$breakpoint-tablet} {
            padding: 0 1.5rem;
        }
    }

    &__final-information {
        padding: 1.5rem 4.8rem;
        background-color: $color-gray-light;
        border-top: 1px solid $color-gray-athens;
        display: flex;
        justify-content: space-between;
        min-height: 6rem;
        position: sticky;
        position: -webkit-sticky;
        bottom: 0;

        @media #{$breakpoint-tablet} {
            padding: 1.5rem;
            text-align: center;
            flex-direction: column;
            min-height: 10rem;
        }
    }

    &__final-information--no-button {
        min-height: auto;
    }

    &__final-details-container {
        @media #{$breakpoint-tablet} {
            min-height: 3rem;
        }
    }

    &__subtitle {
        color: $color-gray-corduroy;
    }

    &__description {
        margin: 1.5rem 0 2rem;
    }

    &__container {
        border-top: 1px solid $color-gray-athens;
        padding-top: 2.5rem;
        display: flex;
        margin-bottom: 2rem;

        @media #{$breakpoint-tablet} {
            flex-direction: column;
            padding-top: 2rem;
        }
    }

    &__edc-logo-container {
        @media #{$breakpoint-tablet} {
            align-items: center;
        }
    }

    &__edc-logo {
        width: 100%;

        @media #{$breakpoint-tablet} {
            max-width: 100%;
        }
    }

    &__title {
        font-weight: bold;
        letter-spacing: toRem(-0.28px);
        width: 30%;
        margin-right: 2.5rem;

        @media #{$breakpoint-tablet} {
            width: 100%;
            margin-right: 0;
        }
    }

    &__text {
        width: 70%;

        &--is-bundle {
            width: 100%;
        }

        @media #{$breakpoint-tablet} {
            margin-top: 1.5rem;
            width: 100%;
        }
    }

    &__edc-images-block {
        display: flex;
        width: 70%;
    }

    &__edc-image-container {
        @media #{$breakpoint-tablet} {
            max-width: 100%;
        }

        &--multiple {
            width: 50%;
            margin-right: toRem(10px);
        }
    }

    &__edc-image-title {
        margin-bottom: toRem(12.5px);
    }

    &__image-container {
        width: 30%;
        margin-right: 2.5rem;

        .base-image {
            max-width: 100%;
        }
    }

    &__green-text {
        width: 70%;

        @media #{$breakpoint-tablet} {
            width: calc(100%);
            margin-top: 1.5rem;
        }
    }

    &__image {
        @media #{$breakpoint-tablet} {
            margin-top: 1.5rem;
        }
    }

    &__price-breakdown-container {
        width: 70%;
        display: flex;
        flex-direction: column;

        @media #{$breakpoint-tablet} {
            margin-top: 1.5rem;
            width: 100%;
        }
    }

    &__price-breakdown {
        background-color: $color-gray-light;
        display: flex;
        justify-content: space-between;
        padding: 0.5rem 1rem;
        margin-bottom: 0.5rem;
    }

    &__final-container {
        display: flex;
        flex-direction: row;
        margin-right: 1rem;

        @media #{$breakpoint-tablet} {
            justify-content: center;
            margin-bottom: 1.5rem;
            margin-right: 0;
        }
    }

    &__final-container--no-button {
        @media #{$breakpoint-tablet} {
            margin-bottom: 0;
        }
    }

    &__line {
        width: 1px;
        margin: 0 1.5rem;
        background-color: $color-gray-iron;
    }
}
