@import "../../styles/mixins";
@import "../../styles/variables";

.ak-logos {
    min-height: toRem(120px);
    background-color: $color-gray-light;

    &__container {
        @include base-container();
        padding: 1rem;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;

        @media #{$breakpoint-tablet} {
            flex-wrap: wrap;
        }
    }

    &__logo-wrapper {
        flex: 1 1;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;

        @media #{$breakpoint-tablet} {
            $item-width: calc(100% / 3);
            flex: 1 1 $item-width;
            max-width: $item-width;
            min-width: 1px;
            margin-bottom: 1rem;
        }
    }

    &__logo {
        height: auto;

        @media #{$breakpoint-mobile-xs} {
            width: 100%;
        }

        @media #{$breakpoint-desktop-only} {
            width: auto;
            height: 5.5rem;
        }
    }
}
