@import "../../styles/mixins";
@import "../../styles/variables";

.ak-plan-modal {
	&__utilities-wrapper{
		margin-top: 2.5rem;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: flex-start;

		@media #{$breakpoint-tablet} {
            flex-direction: column;
            align-items: flex-start;
        }

        li {
            width: 100%;
        }
	}

    &__modal-content {
        width: 100%;
    }

	&__description {
		text-align: left;
	}

	&__utility{
		position: relative;
		margin-bottom: 0.5rem;
	}
}
