@function toRem($size, $font-size-base: 16px) {
    $remSize: $size / $font-size-base;
    @return #{$remSize}rem;
}

@mixin background-image() {
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}

@mixin visually-hidden() {
    border: 0;
    clip: rect(0 0 0 0);
    height: 0.06rem;
    margin: 0.06rem;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 0.06rem;

    &:active,
    &:focus {
        clip: auto;
        height: auto;
        margin: 0;
        overflow: visible;
        position: static;
        width: auto;
    }

    a:focus,
    a:hover {
        position: static;
        width: auto;
        height: auto;
    }
}

.hidden {
    display: none;
}
