@import "../../styles/mixins";
@import "../../styles/variables";

.ak-faq-category {

    &__container {
        padding-top: 2rem;
        padding-bottom: 5rem;
        @include base-container();

        @media #{$breakpoint-desktop-only} {
            padding-top: 5.5rem;
        }
    }

    &__navigation {
        width: 100%;

        @media #{$breakpoint-tablet-and-desktop} {
            width: 15.75rem;
            min-width: 15.75rem;
        }
    }

    &__content {
        width: 100%;

        @media #{$breakpoint-tablet-and-desktop} {
            margin-left: 7.25rem;
        }
    }

    &__description {
        margin-bottom: 2rem;

        @media #{$breakpoint-tablet-and-desktop} {
            margin-bottom: 3rem;
        }
    }

    &__navigation-item {
        margin-bottom: 0.5rem;
        padding: 0.5rem toRem(10px);

        &--active {
            border-left: 3px solid $color-blue-dodger;
            background-color: rgba($color-blue-dodger, 0.04);
        }
    }

    &__link {
        display: block;
        position: relative;
        text-decoration: none;
        margin-bottom: 0.5rem;
        padding: 1.5rem 1.5rem 1.5rem 0;
        background-color: $color-gray-light;

        @media #{$breakpoint-tablet-and-desktop} {
            padding: 1.25rem 2.5rem 1.25rem 1rem;
        }
    }

    &__link-icon {
        right: 0;
        top: 1.5rem;
        position: absolute;

        @media #{$breakpoint-tablet-and-desktop} {
            top: 50%;
            right: 1rem;
            transform: translateY(-50%);
        }
    }

    &__link-wrapper {
        position: relative;

        @media #{$breakpoint-mobile} {
            &:before {
                content: '';
                top: 0;
                left: 50%;
                height: 100%;
                position: absolute;
                width: calc(100% + 2rem);
                transform: translateX(-50%);
                background-color: $color-gray-light;
            }
        }
    }
}
