@import "../variables";
@import "../mixins/utils";

@mixin type-heading-base() {
    font-family: $font-family-primary;
    font-weight: 500;
}

@mixin type-body-base() {
    font-family: $font-family-secondary;
    letter-spacing: normal;
    font-weight: normal;
}

@mixin heading-h1 {
    @include type-heading-base();
    font-size: toRem(40px);
    line-height: toRem(50px);
    letter-spacing: toRem(-1.2px);

    @media #{$breakpoint-desktop-only} {
        font-size: toRem(64px);
        line-height: toRem(96px);
        letter-spacing: toRem(-1.92px);
    }
}

@mixin heading-h2 {
    @include type-heading-base();
    font-size: toRem(32px);
    line-height: toRem(40px);
    letter-spacing: toRem(-0.96px);

    @media #{$breakpoint-desktop-only} {
        font-size: toRem(48px);
        line-height: 1.33;
        letter-spacing: toRem(-1.44px);
    }
}

@mixin heading-h3 {
    @include type-heading-base();
    font-size: toRem(24px);
    line-height: toRem(32px);
    letter-spacing: toRem(-0.72px);

    @media #{$breakpoint-desktop-only} {
        font-size: toRem(32px);
        line-height: toRem(48px);
        letter-spacing: toRem(-0.96px);
    }
}

@mixin heading-h4 {
    @include type-heading-base();
    font-size: toRem(20px);
    line-height: toRem(26px);
    letter-spacing: toRem(-0.6px);

    @media #{$breakpoint-desktop-only} {
        font-size: toRem(24px);
        line-height: toRem(40px);
        letter-spacing: toRem(-0.72px);
    }
}

@mixin subheading {
    @include type-heading-base();
    font-size: toRem(14px);
    line-height: toRem(18px);
    letter-spacing: toRem(-0.28px);
	color: $color-accent;
    @media #{$breakpoint-desktop-only} {
        line-height: toRem(16px);
        letter-spacing: toRem(-0.28px);
    }
}

@mixin cta-text {
    @include type-heading-base();
    font-size: toRem(16px);
    line-height: toRem(24px);
    letter-spacing: toRem(-0.32px);
}

@mixin body-1 {
    @include type-body-base();
    font-size: toRem(18px);
    line-height: toRem(26px);

    @media #{$breakpoint-desktop-only} {
        font-size: toRem(22px);
        line-height: toRem(36px);
    }
}

@mixin body-2 {
    @include type-body-base();
    font-size: toRem(17px);
    line-height: toRem(26px);

    @media #{$breakpoint-desktop-only} {
        font-size: toRem(18px);
        line-height: toRem(32px);
    }
}

@mixin body-3 {
    @include type-body-base();
    font-size: toRem(16px);
    line-height: toRem(23px);

    @media #{$breakpoint-desktop-only} {
        font-size: toRem(16px);
        line-height: toRem(24px);
    }
}

@mixin body-4 {
    @include type-body-base();
    font-size: toRem(14px);
    line-height: toRem(20px);
}

