@import '../../styles/mixins';
@import '../../styles/variables';

.ak-header {
    color: $color-black-woodsmoke;
    background-color: $color-white;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: row;
    width: 100%;
    z-index: $z-index-header;
    border-bottom: 1px solid $color-gray-athens-transparent;
    flex-wrap: wrap;

    &.open {
        border-color: $color-gray-athens;
        z-index: $z-index-header-open;
    }

    &__brand {
        margin-right: auto;
    }

    &__brand .base-icon {
        width: toRem(118px);
    }

    &__nav {
        @include base-container();
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: center;
        height: $header-height;
    }

    &__primary-nav {
        display: flex;
        flex-direction: row;
        margin-left: 1.5rem;
    }

    &__nav-item {
        margin-right: toRem(24px);
    }

    &__nav-item__button {
        @include nav-button();
        padding-right: toRem(24px);
        padding-left: toRem(7px);
        position: relative;
        height: calc(#{$header-height} - 2px);
        border-bottom: 3px solid transparent;
        $caret-width: 5px; //Half of the actual size
        $caret-height: 5px;

        &:after {
            content: '';
            position: absolute;
            display: inline-block;
            top: calc(50% - #{$caret-height});
            right: calc(12px - #{$caret-width});
            border-style: solid;
            border-width: $caret-height $caret-width 0 $caret-width;
            border-color: $color-black-woodsmoke transparent transparent transparent;
            transition: all 0.5s ease;
        }

        &.open,
        &.selected {
            border-color: $color-blue-klein;
            &:after {
                animation: rotate-180 0.25s ease 1 forwards;
                border-color: $color-blue-klein transparent transparent transparent;
            }
            &:hover,
            &:focus {
                color: $color-blue-klein;
            }
        }

        &:before {
            content: '';
            position: absolute;
            bottom: -3px;
            left: 0;
            height: 3px;
            width: 0%;
            background-color: transparent;
            transition: all 0.3s ease-in-out;
        }
        &:not(.open):hover:before,
        &:not(.open):focus:before {
            width: 100%;
            background-color: $color-blue-klein;
        }
    }

    &__additional-content {
        margin-left: auto;
        display: flex;
        flex-direction: row;
        align-items: center;
    }

    &__search-btn {
        @media #{$breakpoint-desktop-only} {
            @include nav-cta-link();
            padding: toRem(8px);
            margin-right: toRem(16px);
            border-radius: 50%;

            &:hover,
            &:focus,
            &:hover:focus {
                background-color: $color-gray-light;
            }
        }
    }

    &__cta {
        @include nav-cta-link();
    }

    &__search-bar {
        position: fixed;
        left: 0;
        width: 100%;
        top: $header-height;
        background-color: $color-white;
        z-index: $z-index-header;
        border-bottom: 1px solid $color-gray-iron;

        &.open {
            display: flex;
            animation: openSearch 0.25s ease-out 1 forwards;
        }

        &.closed {
            display: none;
        }

        .ak-search-form {
            @include base-container();
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
            max-height: toRem(56px);

            &__input {
                @include search-input();
                flex: 1 1;
                width: 100%;
                padding-left: 0.5rem;
                padding-right: 0.5rem;
            }

            &__btn {
                background-color: transparent;
                border: none;
                font-family: $font-family-secondary;
                color: $color-gray-corduroy;
                padding: 1rem 0.5rem;

                .highlight {
                    padding: toRem(2px) toRem(8px);
                    background-color: $color-gray-light;
                    border-radius: 3px;
                }
            }

            &__close-btn {
                padding: 0.5rem;
                margin-left: 0.5rem;
                &:hover,
                &:focus,
                &:hover:focus {
                    background-color: $color-gray-light;
                }
            }
        }
    }

    &__category {
        position: fixed;
        left: 0;
        width: 100%;
        top: $header-height;
        background-color: $color-white;
        z-index: $z-index-header;

        &-container {
            @include base-container();
            $vertical-padding: toRem(40px);
            display: flex;
            flex-direction: row;
            padding-top: $vertical-padding;
            padding-bottom: $vertical-padding;
        }

        &-wrapper,
        &-wrapper.closed {
            opacity: 0;
            transition: all 0.25s ease-in;
        }

        &-wrapper.open {
            opacity: 1;
        }

        &-details {
            max-width: toRem(344px);
            width: toRem(344px);
            display: flex;
            flex-direction: column;
            justify-content: flex-start;
            align-items: flex-start;
        }

        &-description {
            color: $color-gray-corduroy;
            margin-top: 0.5rem;
            width: 100%;
            word-break: break-all;
        }
    }

    &__secondary-nav {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        padding: 0 toRem(24px);
        margin: 0 toRem(-24px) 0;
        flex: 1 0;
    }

    &__secondary-nav-item {
        $item-width: calc(50% - 48px);
        margin: 0 toRem(24px) toRem(24px);
        border: 1px solid transparent;
        padding: toRem(12px);
        flex: 1 1 $item-width;
        max-width: $item-width;
        min-width: $item-width;
        background-color: transparent;
        transition: all 0.25s ease-in-out;
        display: flex;

        &:hover,
        &:focus {
            cursor: pointer;
            background-color: $color-gray-light;
            border-color: $color-gray-athens;
        }
    }

    &__secondary-nav-icon {
        $icon-size: toRem(32px);
        width: $icon-size;
        height: $icon-size;
        margin-right: toRem(12px);
    }

    &__secondary-nav-link {
        @include secondary-nav-link();
    }

    // Mobile

    &__container {
        @include base-container();
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        height: $header-height;
    }

    $nav-btn-size: toRem(24px);
    &__nav-open {
        height: $nav-btn-size;
        width: $nav-btn-size;
    }

    &__nav-close {
        top: 1.5rem;
        position: absolute;
        width: $nav-btn-size;
        height: $nav-btn-size;
        right: $container-mobile-padding;
    }

    &__mobile {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: $color-white;
        animation: fadein 0.25s ease 1 forwards;

        &-content {
            @include base-container();
            height: 100%;
            position: relative;
            $vertical-gap: toRem(80px);
            padding-top: $vertical-gap;
        }

        &-nav {
            height: 100%;
            overflow-y: auto;
        }

        .ak-header__brand {
            position: absolute;
            top: 1rem;
            left: 1rem;
        }

        .ak-header__cta {
            @include accordion-button();
        }

        &-nav-list {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: flex-start;
        }

        &-nav-item {
            @include accordion-button();
            $vertical-padding: toRem(24px);
            width: 100%;
            padding-top: $vertical-padding;
            padding-bottom: $vertical-padding;
            border-top: 1px solid $color-gray-iron;

            &:last-child {
                border-bottom: 1px solid $color-gray-iron;
            }
        }
    }

    &__accordion {
        display: flex;
        flex-direction: column;

        &-btn {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
        }

        &-content {
            height: 0;
            overflow: hidden;
            transition: height 0.25s ease-in-out;
        }

        &-list-item {
            @include secondary-nav-link();
            padding-top: 1rem;
            &:first-child {
                padding-top: toRem(24px);
            }
        }

        &-icon {
            $line-width: 12px;
            $line-height: 2px;
            $icon-size: calc(#{$line-width} * 2);
            background: transparent;
            display: block;
            position: relative;
            padding: $line-width;
            height: $icon-size;
            width: $icon-size;

            &:before,
            &:after {
                content: '';
                width: $line-width;
                height: $line-height;
                background-color: $color-blue-klein;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%) rotate(00deg);
                transition: width 0.15s ease-in, opacity 0.15s ease-in-out;
                transform-origin: center;
                border-radius: 2px;
            }

            &:after {
                transform: translate(-50%, -50%) rotate(90deg);
            }

            &.open:after {
                opacity: 0.5;
                width: $line-height;
            }
        }
    }

    &__header-banner {
        position: relative;
        width: 100%;
        background-color: $color-blue-header;
    }

    &__header-banner-container {
        @include base-container();
        padding-top: toRem(30px);
        padding-bottom: toRem(30px);
        padding-right: 3rem;
        position: relative;

        @media #{$breakpoint-tablet} {
            padding-right: 3rem;
        }
    }

    &__banner-title {
        color: $color-white;
        margin-bottom: toRem(4px);
    }

    &__banner-description {
        color: $color-white;
        margin-bottom: toRem(30px);
    }

    &__banner-cta-link {
        @include nav-cta-link();
        padding: none;
        background-color: transparent;
        color: $color-white;
    }

    &__dismiss-banner {
        position: absolute;
        right: 1.5rem;

        @media #{$breakpoint-tablet} {
            right: 1rem;
        }

        svg {
            path {
                stroke: $color-white;
            }
        }
    }
}
