@import "../../styles/mixins";
@import "../../styles/variables";

.ak-small-hero {
    position: relative;
    min-height: toRem(256px);
    background-color: $color-white;

    .blue {
        background-color: $color-blue-klein;

        .ak-small-hero__subtitle,
        .ak-small-hero__title,
        .ak-small-hero__description {
            color: $color-white;
        }
    }

    &:before {
        content: '';
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        background-image: url('/etc.clientlibs/settings/wcm/designs/akron/clientlib-all/resources/images/small-hero-background-shape.svg');
        background-repeat: no-repeat;
        background-position: center center;
        background-size: cover;
    }

    &__container {
        @include base-container();
        position: relative;
        text-align: center;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-top: toRem(128px);
        padding-bottom: toRem(128px);
    }

    &__title {
        margin-bottom: toRem(8px);
        color: $color-blue-klein;
    }

    &__subtitle {
        @include subheading();
        color: $color-gray-corduroy;
    }

    &__description {
        max-width: toRem(712px);
        color: $color-gray-corduroy;
    }
}

