@import "../../styles/mixins";
@import "../../styles/variables";

.ak-community-programs-frame {

    &__header {
        width: 100%;
        border-bottom: 1px solid $color-gray-iron;
    }

    &__header-container {
        @include base-container();
        $vertical-padding: toRem(48px);

        display: flex;
        flex-direction: row;
        justify-content: space-between;
        padding-top: $vertical-padding;
        padding-bottom: $vertical-padding;

        @media #{$breakpoint-tablet} {
            $vertical-padding: toRem(24px);

            flex-direction: column-reverse;
            padding-top: $vertical-padding;
            padding-bottom: $vertical-padding;
        }

        &__left,
        &__right {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
        }

        &__right {
            align-items: flex-end;
            justify-content: flex-end;
            max-width: toRem(175px);
            @media #{$breakpoint-tablet} {
                $vertical-padding: toRem(24px);

                max-width: unset;
                flex-direction: row;
                align-items: center;
                justify-content: space-between;
                margin-bottom: $vertical-padding;
                padding-bottom: $vertical-padding;

            }
        }
    }

    &__header-components {
        @include base-container();
        padding-top: 2rem;
        padding-bottom: 4rem;
    }

    &__phone-link {
        display: flex;
        padding: 0.5rem;
        margin-top: 0.5rem;
        align-items: center;
        flex-direction: row;

        .base-image {
            $icon-size: toRem(18px);

            width: $icon-size;
            height: $icon-size;
            margin-right: 0.5rem;
        }
    }

    &__filter-container {
        @include base-container();
        $vertical-padding: toRem(24px);

        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        padding-top: $vertical-padding;
        padding-bottom: $vertical-padding;

        &__form {
            width: 100%;
            display: flex;
            border-radius: 4px;
            flex-direction: row;
            align-items: center;
            background-color: $color-white;
            justify-content: space-between;
            border: 1px solid $color-gray-iron;
        }

        &__input {
            @include search-input();
            $horizontal-padding: 1rem;

            flex: 1 1;
            width: 100%;
            border: 1px solid transparent;
            padding-left: $horizontal-padding;
            padding-right: $horizontal-padding;
        }

        &__btn {
            $btn-size: toRem(36px);
            padding: 0;
            border: none;
            min-width: 1px;
            width: $btn-size;
            height: $btn-size;
            border-radius: 50%;
            margin: 0 1rem;
            position: relative;
            color: $color-blue-klein;
            background-color: transparent;

            .base-icon {
                top: 50%;
                left: 50%;
                position: absolute;
                transform: translate(-50%,-50%);
            }

            &:hover, &:focus {
                background-color: $color-gray-light;
            }
        }
    }

    &__title {
        @media #{$breakpoint-tablet} {
            margin-bottom: 0.5rem;
        }
    }

    .ak-community-table {
        width: 100%;
        overflow: hidden;
        position: relative;
        margin-bottom: toRem(112px);

        &:before {
            top: 0;
            left: 0;
            right: 0;
            content: '';
            position: absolute;
            height: toRem(56px);
            background-color: $color-blue-klein;
        }


        &__header {
            position: relative;
            height: toRem(56px);
            background-color: $color-blue-klein;

            .ak-community-table__cell.ak-community-table__cell--header {
                color: $color-white;
            }
        }

        &__row {
            @include base-container;
            $vertical-padding: 2rem;

            display: flex;
            position: relative;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;
            padding-top: $vertical-padding;
            padding-bottom: $vertical-padding;

            &--header {
                $vertical-padding: 1rem;

                padding-top: $vertical-padding;
                padding-bottom: $vertical-padding;
            }

            &:before {
                content: '';
                position: absolute;
                bottom: 0;
                left: 50%;
                height: 1px;
                background-color: $color-gray-iron;
                width: 100vw;
                transform: translateX(-50%);
            }

            &-link {
                $icon-size: toRem(36px);

                width: $icon-size;
                height: $icon-size;
                border-radius: 50%;
                position: relative;
                color: $color-blue-klein;

                .base-icon {
                    top: 50%;
                    left: 50%;
                    position: absolute;
                    transform: translate(-50%,-50%);
                }

                &:hover {
                    background-color: $color-gray-light;
                }
            }
        }

        &__cell {
            $cell-size: calc(95% / 4);
            display: flex;
            min-width: 1px;
            width: $cell-size;
            flex: 1 1 $cell-size;
            max-width: $cell-size;
            flex-direction: column;
            justify-content: flex-start;
            color: $color-gray-corduroy;

            @media #{$breakpoint-tablet} {
                $cell-size: calc(100% - 36px);
                width: $cell-size;
                flex: 1 1 $cell-size;
                max-width: $cell-size;
            }

            &:first-child {
                color: $color-black-woodsmoke;
            }

            &:last-child {
                width: auto;
                flex: 1 0 auto;
                max-width: unset;
                justify-content: flex-end;
            }

            &--gray {
                color: $color-gray-corduroy;
            }
        }
    }
}
