@import '../../styles/mixins';
@import '../../styles/variables';

.ak-enrollment-frame {
    margin-bottom: 2rem;

    //DETAILS-HEADER
    &__details-header {
        height: 9.5rem;
        background-color: $color-gray-light;

        @media #{$breakpoint-mobile} {
            height: 7.5rem;
        }
    }
    &__details-container {
        @include base-container();
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        height: 100%;
    }

    &__left-content,
    &__right-content {
        display: flex;
        flex-direction: row;
        align-items: center;
        height: 100%;
    }

    &__right-content {
        @media #{$breakpoint-mobile} {
            display: none;
        }
    }

    &__col {
        display: flex;
        flex-direction: column;
    }

    &__right-content &__col {
        padding-left: 5.5rem;
    }

    &__details {
        display: flex;
        flex-direction: row;
        align-items: center;

        @media #{$breakpoint-mobile} {
            margin-top: 0.5rem;
        }
    }

    &__details-text {
        color: $color-gray-corduroy;
        font-weight: 600;
        margin-right: 0.5rem;
    }

    &__info-icon {
        height: 1.2rem;
    }

    &__title {
        letter-spacing: toRem(-0.96px);

        @media #{$breakpoint-mobile} {
            font-size: toRem(32px);
        }
    }

    &__field-title {
        margin-top: 0.5rem;
        align-self: flex-end;
    }

    //STATUS
    &__status-bar {
        height: toRem(6px);
        background-color: $color-gray-athens;
        margin-bottom: 3rem;

        @media #{$breakpoint-mobile} {
            margin-bottom: 2rem;
        }
    }

    &__filled {
        height: 100%;
        background-color: $color-blue-dodger;
        transition: all 0.5s ease;
    }

    &__filled-1 {
        width: 25%;
    }

    &__filled-2 {
        width: 50%;
    }

    &__filled-3 {
        width: 75%;
    }

    &__filled-4 {
        width: 100%;
    }

    &__container {
        @include base-container();
    }

    &__container--flex {
        margin-top: 1rem;
        display: flex;

        @media #{$breakpoint-mobile} {
            margin-top: 0.5rem;
        }
    }

    &__panel-left {
        width: 70%;

        @media #{$breakpoint-mobile} {
            width: 100%;
        }
    }

    &__panel-right {
        padding-left: 2.5rem;
        width: 30%;
        margin-top: 1.5rem;

        &--bundle {
            margin-top: 3rem;
        }

        @media #{$breakpoint-mobile} {
            display: none;
        }
    }

    &__cards-container {
        width: 100%;
        margin-top: 1.5rem;

        @media #{$breakpoint-tablet} {
            margin: 0;
        }
    }

    //STEPS
    &__offer-section-title {
        @include subheading();
        font-size: toRem(14px);
        padding: 0 0.75rem 1.5rem;
        color: $color-black-woodsmoke;

        &--card {
            padding: 2rem 0 0;
        }
    }

    &__fields-row {
        margin: 0 -0.75rem;
        width: 100%;
        display: flex;
        justify-content: space-between;

        @media #{$breakpoint-mobile} {
            flex-direction: column;
            margin: 0;
        }
    }

    &__btns-row {
        padding-right: 1.5rem;
        width: 100%;
        display: flex;
        justify-content: space-between;
        margin-top: 2.5rem;
        align-items: center;

        @media #{$breakpoint-mobile} {
            margin-top: 3.5rem;
        }
    }

    &__btns-row--col {
        padding-right: 0;

        @media #{$breakpoint-mobile} {
            flex-direction: column-reverse;
        }
    }

    &__btns-row--col &__btn {
        @media #{$breakpoint-mobile} {
            margin-bottom: 1.5rem;
        }
    }

    &__btn {
        height: 3.5rem;
    }

    &__checkbox-row {
        margin-top: -1.5rem;

        @media #{$breakpoint-mobile} {
            margin-top: 0;
        }
    }

    &__input {
        width: 50%;
        padding: 0 0.75rem;
        position: relative;

        @media #{$breakpoint-mobile} {
            width: 100%;
            padding: 0;
        }
    }

    &__checkbox {
        margin-left: 0.5rem;
    }

    &__element {
        position: relative;
    }

    &__input--full {
        width: 100%;
    }

    &__input--justified-right {
        display: flex;
        justify-content: flex-end;
        flex-direction: column;
    }

    &__back-btn {
        display: flex;
        color: $color-gray-corduroy;
        padding-left: 1.5rem;
    }

    &__chevron {
        position: absolute;
        left: 0;
        top: -3px;
        transform: rotate(90deg);
    }

    &__error-container {
        min-height: 1.5rem;
    }

    &__error-message {
        color: $color-red;
    }

    &__error-message--complete {
        width: 100%;
    }

    &__modal-button {
        color: $color-blue-dodger;
    }

    &__modal-content {
        width: 100%;
        display: flex;
        flex-direction: row;
        align-items: flex-end;
    }

    .base-modal__icon-close {
        width: 1.5rem;
        height: 1.5rem;
    }

    &__customer-number-image {
        max-width: 80%;
        margin: 0 auto;
        display: block;

        @media #{$breakpoint-desktop-only} {
            max-width: 100%;
        }
    }

    &__offer-section-wrapper {
        width: 100%;

        &--bundle {
            margin-top: 3rem;
        }
    }

    //STEP FOUR
    &__disclaimer,
    &__captcha {
        margin-top: 1.5rem;
        background-color: $color-gray-light;
        padding: 0.5rem 1rem;
        border-radius: 4px;
        position: relative;

        @media #{$breakpoint-mobile} {
            margin-top: 1.5rem;
            padding: 1rem 2rem;
        }
    }

    &__captcha {
        padding: 0.5rem 0;
        background-color: white;

        @media #{$breakpoint-mobile} {
            .ak-enrollment-frame__error-message {
                width: 100%;
                text-align: center;
            }

            & > div > div > div {
                margin: 0 auto;
            }
        }
    }

    //CARD
    &__card {
        margin-bottom: 1.5rem;
        border: solid 1px $color-gray-iron;
        border-radius: 4px;
        padding: 1.5rem;
        position: relative;
        cursor: pointer;

        .base-title {
            margin-right: 2rem;
        }

        @media #{$breakpoint-mobile} {
            margin-bottom: 0;
            margin-top: 1.5rem;
        }
    }

    &__action-icon {
        position: absolute;
        width: 1.5rem;
        top: 1.5rem;
        right: 1.5rem;
    }

    &__row {
        width: 100%;
        display: flex;

        @media #{$breakpoint-mobile} {
            flex-direction: column;
        }
    }

    &__line {
        margin-top: 1.5rem;
        height: 1px;
        width: 100%;
        background-color: $color-gray-iron;
    }

    &__detail-container {
        width: 50%;
        margin-top: 1.5rem;
    }

    &__label {
        color: $color-gray-corduroy;
        margin-bottom: 0.25rem;

        @media #{$breakpoint-mobile} {
            margin-bottom: 0.5rem;
        }
    }
}
