.base-image {
    width: auto;
    height: auto;

    &--full {
        width: 100%;
    }

    &--half {
        width: 50%;
    }

    &--small {
        width: 25%;
    }

    &--cover {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &--contain {
        width: 100%;
        height: 100%;
        object-fit: contain;
    }

    &--center {
        margin: 0 auto;
        display: block;
    }
}
