@import "../../../styles/mixins";
@import "../../../styles/variables";

.base-input {
    display: flex;
    flex-direction: column;

    &__label {
        @include body-4();
        margin-bottom: .5rem;
    }

    &__input {
        @include base-input();
    }

    &__input--invalid {
        border-color: $color-red;
    }
}
