@import '../../styles/mixins';
@import '../../styles/variables';

/*
ASPECT RATIO FORMULA
aspect ratio = original width  original height
adjusted height = 100vw  aspect ratio
*/

.ak-footer {
    margin-top: 2rem;
    color: $color-white;

    @media #{$breakpoint-desktop-only} {
        margin-top: 4rem;
    }

    &__logo {
        height: toRem(37px);

        @media #{$breakpoint-desktop-only} {
            height: toRem(47px);
        }
    }

    &__navigation-wrapper {
        overflow: hidden;
        position: relative;
        padding-top: 4.5rem;
        @include background-image();
        background-image: url('/etc.clientlibs/settings/wcm/designs/akron/clientlib-all/resources/images/footer-background-mobile.svg');

        @media #{$breakpoint-desktop-only} {
            padding-top: 7.5rem;
            background-image: url('/etc.clientlibs/settings/wcm/designs/akron/clientlib-all/resources/images/footer-background-desktop.svg');
        }
    }

    &__curve {
        left: 50%;
        top: -1px;
        width: 101vw;
        position: absolute;
        transform: translateX(-50%);
        height: calc(100vw / (375 / 35));
        @include background-image();
        background-image: url('/etc.clientlibs/settings/wcm/designs/akron/clientlib-all/resources/images/footer-white-curve-mobile.svg');

        @media #{$breakpoint-tablet-and-desktop} {
            height: calc(100vw / 25.26);
            background-image: url('/etc.clientlibs/settings/wcm/designs/akron/clientlib-all/resources/images/footer-white-curve-desktop.svg');
        }
    }

    &__navigation {
        @include base-container();
    }

    &__columns {
        justify-content: space-between;
    }

    &__column-title {
        margin-bottom: 1.5rem;
    }

    &__column-link {
        margin-bottom: 1rem;

        &:last-child {
            margin-bottom: 0;
        }
    }

    &__information {
        justify-content: space-between;

        @media #{$breakpoint-desktop-only} {
            margin-bottom: 2rem;
        }
    }

    &__title {
        font-family: $font-family-secondary;
    }

    &__contacts {
        position: relative;
        margin-bottom: 2rem;
        justify-content: flex-end;
        flex-wrap: wrap;

        @media #{$breakpoint-desktop-only} {
            margin: 0;
        }
    }

    &__contact {
        position: relative;
        padding-left: 2rem;
        margin-bottom: 2rem;

        &--extra {
            margin-left: toRem(54px);
        }

        &:last-child {
            margin-bottom: 0;
        }

        @media #{$breakpoint-desktop-only} {
            text-align: right;
            margin-bottom: 0;
        }
    }

    &__hours-of-operation {
        width: 100%;
        font-weight: 500;
        margin-top: 1.5rem;
        overflow-wrap: break-word;

        @media #{$breakpoint-tablet} {
            font-weight: normal;
            color: $color-gray-transparent;
            margin-top: 0;
        }
    }

    &__contact-info {
        margin-bottom: 0.5rem;
    }

    &__contact-image {
        left: 0;
        width: 1.5rem;
        height: 1.5rem;
        position: absolute;
    }

    &__disclaimer-wrapper {
        background-color: $color-primary;
        @media #{$breakpoint-desktop-only} {
            background-color: rgba($color-primary, 0.9);
        }
    }

    &__disclaimer-left {
        display: flex;
        flex-direction: row;
        align-items: center;

        @media #{$breakpoint-mobile} {
            flex-direction: column;
        }
    }

    &__disclaimer-right {
        @media #{$breakpoint-mobile} {
            margin-top: 2rem;
        }
    }

    &__disclaimer {
        @include base-container();
        padding-top: 2rem;
        padding-bottom: 2rem;
        justify-content: space-between;

        @media #{$breakpoint-desktop-only} {
            opacity: 0.85;
            padding-top: 1.5rem;
            padding-bottom: 1.5rem;
        }
    }

    &__copyright {
        text-align: center;
        line-height: 1.5rem;
    }

    &__privacy-link {
        opacity: 0.5;
        position: relative;
        margin-right: 1rem;

        &:first-child {
            margin-left: 3rem;

            &:before {
                top: 50%;
                content: '|';
                position: absolute;
                left: toRem(-25px);
                transform: translateY(-50%);
            }

            @media #{$breakpoint-mobile} {
                margin-left: 0;

                &:before {
                    display: none;
                }
            }
        }

        &:after {
            top: 50%;
            content: '';
            position: absolute;
            right: toRem(-12px);
            transform: translateY(-50%);
        }

        &:last-child {
            &:after {
                content: '';
            }
            margin-right: 0;
        }

        @media #{$breakpoint-tablet-and-desktop} {
            opacity: 1;
        }
    }

    &__separator {
        height: 0;
        border: 0;
        opacity: 0.3;
        margin: 2rem 0;
        border-top: 1px solid $color-footer-separator;

        &--no-margin {
            margin: 0;
        }

        @media #{$breakpoint-desktop-only} {
            margin: 4rem 0 2rem 0;
            &--no-margin {
                margin: 0;
            }
        }
    }

    &__social-link {
        display: block;
        border-radius: 50%;
        width: toRem(36px);
        height: toRem(36px);

        &:hover,
        &:focus {
            background-color: $color-blue-darker-navy;
        }
    }
}
