@import '../../../styles/mixins';

.base-drawer {
    &__overlay {
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        position: fixed;
        overflow: hidden;
        visibility: visible;
        z-index: $z-index-modal-overlay;
        backdrop-filter: blur(0.4375rem);
        -webkit-backdrop-filter: blur(0.4375rem);
        background-color: $color-black-transparent;
    }

    &__container {
        top: 0;
        width: 50%;
        height: 100%;
        position: fixed;
        overflow-y: auto;
        z-index: $z-index-modal-elements;
        animation: drawer-content-open 0.5s ease-out 1 forwards;

        &--close {
            animation: drawer-content-close 0.5s ease-out 1 forwards;
        }

        @media #{$breakpoint-mobile} {
            left: 0;
            width: 100%;
            animation: none;
        }
    }

    &__container-content {
        width: 100%;
        height: 100%;
        padding-top: 4.8rem;
        background-color: $color-white;

        @media #{$breakpoint-mobile} {
            padding-top: 5rem;
        }
    }

    &__container-button {
        top: 0;
        width: 2.75rem;
        height: 2.75rem;
        left: -2.875rem;
        position: absolute;
        z-index: $z-index-modal-top-elements;
    }

    &__btn-close {
        top: 0;
        border: none;
        position: fixed;
        border-radius: 0;
        background-color: $color-white;

        @media #{$breakpoint-mobile} {
            top: 1rem;
            left: toRem(11px);
            position: absolute;
        }
    }

    &__content {
        height: 100%;
    }
}
