@import "../../../styles/mixins";
@import "../../../styles/variables";

.base-radio-button {

    &__legend {
        @include body-4();
        margin-bottom: .5rem;
    }

    &__container {
        display: flex;
    }

    &__container--direction-col {
        flex-direction: column;
    }
    &__container--direction-row {
        flex-direction: row;

        @media #{$breakpoint-mobile} {
            flex-direction: column;
        }
    }
    &__container--direction-col-reverse {
        flex-direction: column-reverse;
    }
    &__container--direction-row-reverse {
        flex-direction: row-reverse;
        @media #{$breakpoint-mobile} {
            flex-direction: column-reverse;
        }
    }

    &__label {
        cursor: pointer;
        align-items: center;
        padding: 1rem;
        background-color: $color-gray-light-transparent;
    }

    &__icon {
        position: absolute;
    }

    &__text {
        @include body-4();
        margin-left: 2.5rem;
    }
    &__button {
        margin-right: 1rem;
        margin-bottom: .5rem;
    }

    &__input {
        opacity: 0;
        position: absolute;
    }

    &__input + &__label {
        .unselected {
            opacity: 1;
        }

        .selected {
            opacity: 0;
        }
    }

    &__input:checked + &__label {
        background-color: $color-gray-light;

        .unselected {
            opacity: 0;
        }

        .selected {
            opacity: 1;
        }
    }

    &__input:focus + &__label {
        background-color: $color-white-lilac;
    }

    &__input:checked:focus + &__label {
        background-color: $color-white-lilac;
    }
}
