@import "../../styles/mixins";
@import "../../styles/variables";

.ak-icon-card {
    width: 100%;
    height: 100%;

	&__container {
        height: 100%;
		padding: 1rem;
		position: relative;
        border-radius: toRem(3px);
        border: $color-gray-iron toRem(1px) solid;

        &--left-aligned  {
			align-items: flex-start;
		}
	}

	&__icon {
        width: 2rem;
        height: auto;
		margin-bottom: 1rem;

		&--top-padding {
			margin-top: 1.5rem;
		}
	}

	&__description {
        text-align: center;
        margin-bottom: toRem(40px);

		&--left-aligned  {
			text-align: left;
			margin-bottom: toRem(32px);

			&--no-cta {
				margin-bottom: toRem(20px);
			}
		}
	}

	&__title {
		margin-bottom: toRem(16px);
		word-break: break-word;
		text-align: center;

		&--left-aligned  {
			margin-bottom: toRem(8px);
			text-align: left;
		}
	}

	&__cta {
        margin: auto 0 0 0;
        word-break: break-all;
        font-family: $font-family-primary;

        &--link {
			color: $color-blue-dodger;
			cursor: pointer;
		}
	}

    &__cta-text {
        font-family: $font-family-primary;
    }
}
