@import '~swiper/swiper';

.ak-image-carousel {
    max-height: 31.25rem;
    min-height: 24rem;
    max-width: 100%;
    position: relative;
    margin-top: 2rem;

    &__hidden {
        display: none;
    }

    &__subtitle {
        margin-bottom: 1rem;
    }

    &__container {
        position: relative;
        height: 24rem;
    }

    &__image-container {
        width: 100%;
        height: 11rem;
    }

    &__image {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: 50% 0;
    }

    &__text {
        display: none;
    }

    &__caption {
        margin-top: 1.5rem;
        color: $color-gray-corduroy;
    }

    &__button-prev, &__button-next {
        width: 3.25rem;
        min-width: 3.25rem;
        height: 3.25rem;
        border-radius: 100%;
        position: absolute;
        top: 17rem;
        z-index: $z-index-10;
        padding: 0;
    }

    &__button-prev {
        left: 2rem;
    }

    &__button-next {
        left: 6.25rem;
    }

    &__icon {
        margin: 0 auto;
    }

    .swiper-container {
        width: 100%;
        height: 100%;
    }

    .swiper-slide-active {
        .ak-image-carousel__text {
            display: block;
        }
    }

    .swiper-slide-prev {
        width: 1rem !important; //Overwrite swiper width
    }

    .swiper-slide-active {
        width: calc(100% - 5rem) !important; //Overwrite swiper width
    }

    .swiper-slide-next {
        width: 1rem !important; //Overwrite swiper width
    }

    @media #{$breakpoint-desktop-only} {
        max-height: 42rem;
        min-height: 33rem;
        margin-top: 3rem;

        &__container {
            height: 33rem;
        }

        &__image-container {
            height: 25rem;
        }

        &__text {
            width: calc(100% - 8rem);
        }

        &__caption {
            margin-top: 2.5rem;
        }

        &__button-prev, &__button-next {
            bottom: 3.5rem;
            top: unset;
            left: unset;
        }

        &__button-prev {
            right: 27.25rem
        }

        &__button-next {
            right: 23rem
        }

        .swiper-slide-prev {
            width: 21.25rem !important; //Overwrite swiper width
        }

        .swiper-slide-active {
            width: calc(100% - 45.5rem) !important; //Overwrite swiper width
        }

        .swiper-slide-next {
            width: 21.25rem !important; //Overwrite swiper width
        }
    }
}

