@import "../../../styles/mixins";
@import "../../../styles/variables";

.base-file-upload {
	&__wrapper {
		width: 100%;
		border-radius: 0.25rem;
		border: 1px dashed $color-gray-iron;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		padding: 2rem;

		&--error{
			border-color: $color-red;
		}
	}

	&__input {
		display: none;
		&:hover {
			border-color: unset;
		}
	}

	&__error-message {
		color: $color-red;
        position: absolute;
        bottom: -1.5rem;
	}

	&__label {
		margin-bottom: .5rem;
	}

	&__text {
		display: flex;
		align-items: center;
		white-space: nowrap;
		&--left{
			color: $color-gray-corduroy;
		}

		&--right{
			background: transparent;
			padding: 0;
			min-width: unset;
			margin-left: .5rem;
			color: $color-blue-dodger;
			&:hover, &:focus, &:hover:focus {
				text-decoration: underline;
			}
		}
	}

	&__icon {
		margin-bottom: 1rem;
	}

	&__file {
		animation-name: fadein;
		animation-iteration-count: 1;
		animation-timing-function: ease-in;
		animation-duration: .4s;
		display: flex;
		justify-content: space-between;
		align-items: center;
		background-color: $color-gray-light;
		border: 1px solid $color-gray-iron;
		padding: .5rem 1rem;
		margin-top: 1rem;
		border-radius: toRem(4px);
		width: 100%;

		&--left {
			display: flex;
		}

		&--text {
			max-width: toRem(550px);
			display: inline-block;
			overflow: hidden;
			white-space: nowrap;
			text-overflow: ellipsis;
			margin-left: 1rem;

			@media #{$breakpoint-tablet} {
				max-width: toRem(350px);
				display: inline-block;
				overflow: hidden;
				white-space: nowrap;
				text-overflow: ellipsis;
			}
			@media #{$breakpoint-mobile} {
				max-width: toRem(180px);
			}
		}

		&--close {
			padding: 0.5rem;
			margin-left: 0.5rem;
			&:hover, &:focus, &:hover:focus {
				background-color: $color-gray-light;
			}
		}
	}
}

