@import '../../../styles/mixins';

.base-title {
    &--type-h1 {
        @include heading-h1();
    }

    &--type-h2 {
        @include heading-h2();
    }

    &--type-h3 {
        @include heading-h3();
    }

    &--type-h4 {
        @include heading-h4();
    }

    &--type-cta {
        @include cta-text();
    }

    &--type-subheading {
        @include subheading();
    }

    &--alignment-center {
        text-align: center;
    }

    &--alignment-right {
        text-align: right;
    }
}
