@import "../../styles/mixins";
@import "../../styles/variables";

.ak-pagination {
    background-color: $color-white;
    width: 100%;

    &__nav {
        @include base-container();
        $vertical-padding: toRem(64px);
        padding-top: $vertical-padding;
        padding-bottom: $vertical-padding;
        display: flex;
        flex-direction: row;
    }

    &__nav-list {
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        width: 100%;
    }

    &__nav-item {
        display: flex;

        &.current  .ak-pagination__page-number {
            font-weight: bold;
        }

        &.ellipsis:before {
            content: '...';
            @include cta-text();
            display: flex;
            justify-content: center;
            align-items: center;
            padding-left: toRem(24px);
            padding-right: toRem(12px);
        }
    }

    &__arrow-btn {
        $btn-size: toRem(64px);
        border-radius: 50%;
        width: $btn-size;
        min-width: $btn-size;
        height: $btn-size;
        padding: toRem(12px);
        position: relative;

        .base-icon {
            width: toRem(7px);
            height: toRem(14px);
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%,-50%);
        }
    }

    &__page-number {
        min-width: unset;
        padding-right: 0.5rem;
        padding-left: 0.5rem;
        margin: 0 0.25rem;
    }

    &__prev {
        margin-right: toRem(24px);
    }
    &__next {
        margin-left: toRem(24px);
    }
}
