@import "../../styles/mixins";
@import "../../styles/variables";

.ak-hero {
    $hero-animation-duration: 0.5s;
    $hero-text-animation: fade-in-slide $hero-animation-duration ease-out 1 forwards;
    position: relative;
    background-size: 100%, 50%;
    background-repeat: no-repeat;
    background-position: top center;

    &__container {
        position: relative;
        text-align: center;
        display: flex;
        flex-direction: column;
        align-items: center;
        height: toRem(720px);

        &-top {
            @include base-container();
            position: absolute;
            top: 25%;
            left: 50%;
            transform: translate(-50%, -50%);
            max-height: 50%;
            z-index: $z-index-10;
            @media #{$breakpoint-tablet} {
                text-align: left;
            }
        }

        &-bottom {
            @include base-container();
            position: absolute;
            top: 46%;
            max-height: 50%;
            text-align: center;
        }

        &--publish {
            height: calc(100vh - #{$header-height});
            min-height: toRem(720px);
        }
    }

    &__title {
        margin-bottom: toRem(6px);
        white-space: pre-wrap;

        .highlight {
            color: $color-blue-klein;
        }

        @media #{$breakpoint-tablet} {
            margin-bottom: 1rem;
        }

        .first, .second, .third {
            opacity: 0;
            animation: $hero-text-animation;
            display: inline-block;
        }

        .second {
            animation-delay: calc(#{$hero-animation-duration} / 3);
        }

        .third {
            animation-delay: calc((#{$hero-animation-duration} / 3) * 2);
        }
    }

    &__subtitle {
        animation: $hero-text-animation;
    }

    &__description {
        color: $color-white;

        &-text:after {
            content: ' ';
            display: inline;
        }

        &-link.base-link{
            color: $color-white;
            font-weight: bold;
        }

        @media #{$breakpoint-tablet} {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
        }
    }

    &__bottom-image-container {
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        top: 50%;
		overflow: hidden;
    }

    &__bottom-image {
        position: relative;
        height: 100%;
        width: 100%;
        background-size: cover;
        background-position: center center;
        background-repeat: no-repeat;
        animation: zoom-in $hero-animation-duration ease-in-out 1 forwards;

        &:before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            background-image: $image-overlay-gradient;
        }
    }

    .ak-zip-code-form {
        width: 100%;
        max-width: toRem(600px);
        margin: 0 auto 1.5rem auto;

        &--invalid {
            margin-bottom: 0;
        }
    }
}

