@import "../../styles/mixins";
@import "../../styles/variables";

.ak-image-card {

    @media #{$breakpoint-desktop-only} {
        max-width: toRem(528px);
    }

    &:not(.parbase){
        height: 100%;
    }

    &__container {
        height: 100%;
        display: flex;
        border-radius: 3px;
        text-align: center;
        align-items: center;
        padding: toRem(24px);
        flex-direction: column;
        justify-content: center;
        background-color: $color-white;
        border: 1px solid $color-gray-iron;

        &.left {
            text-align: left;
            justify-content: flex-start;
        }
    }

    &__image {
        height: toRem(209px);

        @media #{$breakpoint-desktop-only} {
            height: toRem(360px);
        }
    }

    &__title {
        margin-top: toRem(24px);
    }

    &__description {
        margin-top: toRem(8px);
    }

    &__profile {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        margin-top: toRem(24px);

        &-thumbnail {
            $thumbnail-size: toRem(32px);
            border-radius: 50%;
            width: $thumbnail-size;
            height: $thumbnail-size;
            margin-right: toRem(12px);
        }
    }

    .left &__profile {
        justify-content: flex-start;
    }

    &__cta {
        margin-top: auto;
        width: 100%;
    }

    &__content {
        width: 100%;
        margin-bottom: toRem(25px);
    }
}

