@import "../../../styles/mixins";
@import "../../../styles/variables";

.base-select {

    position: relative;

    &__label {
        @include body-4();
        margin-bottom: .5rem;
    }

    &__container {
        position: relative;
    }

    &__select {
        @include body-3();
        width: 100%;
        border: 1px solid $color-gray-iron;
        border-radius: 4px;
        padding: 1rem;
        text-align: left;
        color: $color-gray-corduroy;
        position: relative;
        z-index: $z-index-10;
        background-color: transparent;
        -moz-appearance:none; /* Firefox */
        -webkit-appearance:none; /* Safari and Chrome */

        &:hover {
            border-color: $color-blue-transparent;
        }

        &:focus {
            border-color: $color-blue-klein;
            outline: none;
        }

        &:disabled {
            background-color: $color-gray-light;
            cursor: not-allowed;
        }
    }

    &__select.selected {
        color: $color-black-woodsmoke;
    }

    &__select--invalid {
        border-color: $color-red;
    }

    &__option {
        color: $color-black-woodsmoke;
    }

    &__default {
        display: none;
    }

    &__icons {
        position: absolute;
        right: 1rem;
        top: 1rem;
        z-index: $z-index-1;
    }

    &__icon--open {
        display: none;
    }

    &__container.open &__icon--open {
        display: block;
    }

    &__container.open &__icon--closed {
        display: none;
    }
}
