@import "../../../styles/mixins";

.base-text {
    &--level-1 {
        @include body-1();
    }

    &--level-2 {
        @include body-2();
    }

    &--level-3 {
        @include body-3();
    }

    &--level-4 {
        @include body-4();
    }

    &--type-cta {
        @include cta-text();
    }

    &--type-sub {
        @include subheading();
    }

    &--alignment-center {
        text-align: center;
    }

    &--alignment-right {
        text-align: right;
    }

    a {
        color: $color-blue-klein;
    }
}

