@import "../mixins/utils";
@import "../../styles/variables";

@mixin base-button() {
    text-align: center;
    min-width: 10.5rem;
    padding: 0.875rem 2.5rem;
    display: inline-block;
    border-radius: toRem(4px);
    border: toRem(2px) solid $color-primary;

    &:focus {
        outline: none;
        box-shadow: 0 0 0 toRem(1px) $color-white, 0 0 0 toRem(4px) $color-focus-inputs,
    }
}

@mixin nav-button() {
    font-size: toRem(14px);
    line-height: 1.43;
    letter-spacing: normal;
    background-color: transparent;
    color: $color-black-woodsmoke;
    font-weight: normal;
    font-family: $font-family-secondary;
}

@mixin accordion-button() {
    font-size: 1rem;
    line-height: 1.5;
    letter-spacing: normal;
    background-color: transparent;
    color: $color-black-woodsmoke;
    font-weight: 500;
    letter-spacing: -0.32px;
    font-family: $font-family-primary;
}
