@import "../../../styles/mixins";
@import "../../../styles/variables";

.base-modal {
	&__overlay {
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        position: fixed;
        z-index: $z-index-modal-overlay;
		backdrop-filter: blur(toRem(7px));
		background-color: rgba(17, 20, 34, 0.6);
	}

	&__content {
		width: 100%;
		height: 100%;
		display: flex;
        align-items: center;
        flex-direction: column;
		justify-content: center;
	}

	&__header {
		flex-direction: column;
		position: absolute;
		top: 0;
		right: 0;
	}

	&__btn-close {
        $btn-size: toRem(40px);
		background: $color-white;
		border-radius: 50%;
        position: relative;
		width: $btn-size;
		height: $btn-size;
		color: $color-blue-klein;
		font-size: toRem(14px);
		margin-right: toRem(40px);
		margin-top: toRem(40px);

		@media #{$breakpoint-mobile} {
			margin-right: toRem(20px);
			margin-top: toRem(20px);
		}
	}

	&__icon-close {
        $icon-size: toRem(18px);

        top: 50%;
        left: 50%;
		width: $icon-size;
		height: $icon-size;
        position: absolute;
        transform: translate(-50%, -50%);
	}

    &__modal-body {
        width: 100%;
        border-radius: 3px;
        margin: 50vh auto 0;
        padding: toRem(24px);
        max-width: toRem(528px);
        background: $color-white;
        transform: translateY(-50%);

        @media #{$breakpoint-tablet} {
            max-width: calc(100% - #{$container-mobile-padding * 2});
        }
    }

    &__modal-header {
        width: 100%;
        display: flex;
        flex-direction: row;
        margin-bottom: toRem(24px);
        justify-content: space-between;

        .base-modal__btn-close {
            margin: 0 0 0 0;
            background-color: $color-white;
            border-color: transparent;
        }
    }

	.rounded {
		border-radius: 50%;
	}
}
