@import "../../styles/mixins";
@import "../../styles/variables";

.ak-search-bar {
	position: fixed;
	left: 0;
	width: 100%;
	top: $header-height;
	background-color: $color-white;
	z-index: $z-index-top-element;
	border-bottom: 1px solid $color-gray-iron;
	padding-top: 1px;

	&.open {
		display: flex;
		animation: openSearch 0.25s ease-out 1 forwards;
	}

	&.closed {
		display: none;
	}

	.ak-search-form {
		@include base-container();
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
		max-height: toRem(56px);


		&__input {
			@include search-input();
			flex: 1 1;
			width: 100%;
			padding-left: 0.5rem;
			padding-right: 0.5rem;
		}

		&__btn {
			border: none;
			font-family: $font-family-secondary;
			color: $color-white;
			padding: 1rem 0.5rem;
			margin: 0 toRem(8px);
			height: 100%;

			@media #{$breakpoint-tablet} {
				padding: toRem(8px);
				border-radius: 50%;
                background-color: transparent;
                height: 2rem;
			}
		}

		&__close-btn {
			padding: 0.5rem;
			margin-left: 0.5rem;
			&:hover, &:focus, &:hover:focus {
				background-color: $color-gray-light;
			}
		}
	}
}

