@import "../../../styles/mixins";

.base-container {
    width: 100%;
    overflow: hidden;
    position: relative;

    &__content {
        @include base-container();

        padding-top: 4rem;
        padding-bottom: 4rem;

        @media #{$breakpoint-mobile} {
            padding-top: 3.5rem;
            padding-bottom: 3.5rem;
        }
    }
}
