@import "../../styles/mixins";
@import "../../styles/variables";

.ak-article-cards-list {
    width: 100%;

    &__container {
        @include base-container();
        $vertical-padding: toRem(65px);
        display: flex;
        flex-direction: column;
        padding-top: $vertical-padding;
        padding-bottom: $vertical-padding;
    }

    &__list {
        display: flex;
        flex-direction: column;
        justify-content: center;
        margin: toRem(-18px) 0;

        @media #{$breakpoint-desktop-only} {
            flex-direction: row;
            flex-wrap: wrap;
            justify-content: flex-start;
            margin: toRem(-18px) toRem(-12px);
        }
    }

    &__list-item {
        margin:  toRem(18px) auto;

        @media #{$breakpoint-desktop-only} {
            $card-size: calc( 50% - #{toRem(24px)});
            flex: 1 1 $card-size;
            max-width: $card-size;
            width: $card-size;
            min-width: 1px;
            margin: toRem(18px) toRem(12px);
        }
    }
}
