.base-box {
    display: flex;

    &--align {
        &-left {
            align-items: flex-start;
        }

        &-center {
            align-items: center;
        }

        &-right {
            align-items: flex-end;
        }
    }

    &--justify {
        &-left {
            justify-content: flex-start;
        }

        &-center {
            justify-content: center;
        }

        &-right {
            justify-content: flex-end;
        }
    }

    &--direction {
        &-col {
            flex-direction: column;
        }

        &-row {
            flex-direction: row;
        }

        &-col-reverse {
            flex-direction: column-reverse;
        }

        &-row-reverse {
            flex-direction: row-reverse;
        }
    }
}
