@import "./utils";


#mainContent {
    padding-top: $header-height;
}

#mainContent.enrollment-main-content {
    padding-top: $enrollment-header-height;
    
    @media #{$breakpoint-mobile} {
        padding-top: $enrollment-header-mobile-height;
    }
}

.aem-AuthorLayer-Preview {
    ak-header {
        width: 100%;
    }
}

.aem-AuthorLayer-Edit {

    .cq-Editable-dom:not(.ak-grid-row__column):not(.ak-offer) {
        width: 100%;
    }

    //Offer Box on edit mode
    .ak-offer {
        min-height: 22rem;
    }

    .ak-offer-container {
        padding: 2rem;
    }

    .parbase.ak-icon-card,
    .parbase.ak-biography-card,
    .parbase.ak-image-card {
        height: auto;
    }

    .ak-header,
    .ak-enrollment-header{
        position: static;
        z-index: unset;
    }

    ak-header {
        width: 100%;
    }

    #mainContent {
        padding-top: 0;
    }

    #mainContent.enrollment-main-content {
        padding-top: 0;
    }
}

// To be used with SR labels.
.sr-only {
    @include visually-hidden();
}

.ak-skip-link {
    @extend .sr-only;
    &:focus {
        position: fixed;
        width: auto;
        height: auto;
        top: 0;
        left: toRem(20px);
        display: block;
        z-index: $z-index-skip-link ;
        background: transparent;
        color: $color-black-woodsmoke;
        font-family: $font-family-secondary;
        clip: auto;
    }
}
