@import "../../styles/mixins";
@import "../../styles/variables";

.ak-enroll-by-phone {
    &__container {
		display: flex;
		align-items: center;
		flex-wrap: wrap;
	}

	&__title {
		margin-right: 1rem;

		&--stacked {
			width: 100%;
			margin-bottom: 0.5rem;
		}
	}

	&__phone-wrapper {
		background-color: $color-gray-light;
		padding: toRem(11px);
		display: flex;
		align-items: center;
	}

	&__icon {
		width: toRem(18px);
		color: $color-blue-klein;
		margin-right: toRem(11px);
	}

	&__phone-number {
		color: $color-blue-klein;
	}
}
