@import "../variables";

@mixin base-container() {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    padding-left: $container-regular-padding;
    padding-right: $container-regular-padding;
    max-width: calc(#{$container-max-size} + (#{$container-regular-padding} * 2));

    @media #{$breakpoint-tablet} {
        padding-left: $container-mobile-padding;
        padding-right: $container-mobile-padding;
    }
}

@mixin skinny-container() {
    @include base-container();
    max-width: calc(#{$container-skinny-max-size} + (#{$container-regular-padding} * 2));
}
