@import "../../styles/mixins";
@import "../../styles/variables";

.ak-article-frame {
    &__content {
        display: flex;
        flex-direction: column;
        width: 100%;
        justify-content: center;
        align-items: center;
    }

    &__header {
        @include base-container();
        $vertical-padding: toRem(64px);
        padding-top: $vertical-padding;
        padding-bottom: $vertical-padding;
    }

    &__title {
        margin-bottom: toRem(32px);
    }

    &__profile {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: flex-start;
        margin-top: toRem(24px);

        &-thumbnail {
            $thumbnail-size: toRem(40px);
            border-radius: 50%;
            width: $thumbnail-size;
            height: $thumbnail-size;
            margin-right: 1rem;
            object-fit: cover;
        }

        &-date {
            color: $color-gray-corduroy;
        }

        &-info {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
        }
    }

    &__main-image {
        object-fit: cover;
        height: toRem(211px);
        margin-bottom: toRem(100px);

        @media #{$breakpoint-desktop-only} {
            height: toRem(810px);
            margin-bottom: toRem(128px);
        }
    }

    &__article-content {
        @include skinny-container();
    }
}
