@import '../../styles/mixins';

.ak-contact-us {
    &__container {
        width: 100%;
        display: flex;
        flex-direction: row;
    }

    //Form
    &__form {
        display: flex;
        flex-direction: column;
        width: 70%;
        margin: 0 -0.75rem;
        padding-right: 1.8rem;

        @media #{$breakpoint-mobile} {
            width: 100%;
            padding: 0;
            margin: 0;
        }
    }

    &__row {
        display: flex;
        flex-direction: row;
        width: 100%;
        margin-top: 1.7rem;

        @media #{$breakpoint-mobile} {
            flex-direction: column;
            width: 100%;
            margin-top: 0;
        }
    }

    &__row--aligned-right {
        justify-content: flex-end;
        position: relative;

        @media #{$breakpoint-mobile} {
            flex-direction: row;
            width: auto;
            margin-top: 2.5rem;
        }
    }

    &__subtitle {
        color: $color-gray-corduroy;
    }

    &__input {
        padding: 0 0.75rem;
        display: flex;
        flex-direction: column;
        width: 50%;
        position: relative;

        @media #{$breakpoint-mobile} {
            width: 100%;
            padding: 0;
            margin-top: 1.8rem;
        }
    }

    &__input--full {
        width: 100%;
    }

    &__radio-button-container {
        display: flex;
        flex-direction: row;
        width: 100%;

        @media #{$breakpoint-mobile} {
            flex-direction: column;
        }
    }

    &__error-message {
        color: $color-red;
        position: absolute;
        bottom: -1.5rem;
    }

    &__error-message--position-end {
        bottom: -2rem;
        right: 1rem;
    }

    &__submit {
        margin-right: 1rem;
    }

    //Right Panel
    &__right-panel {
        padding-left: 1.8rem;
        margin-top: 5.5rem;
        width: 30%;

        @media #{$breakpoint-mobile} {
            display: none;
        }
    }

    &__captcha {
        @media #{$breakpoint-mobile} {
            .ak-contact-us__error-message {
                width: 100%;
                text-align: center;
            }

            & > div > div > div {
                margin: 0 auto;
            }
        }
    }
}
