@import "../../styles/mixins";
@import "../../styles/variables";

.ak-plans-frame {

    &__header {
        width: 100%;
        border-bottom: 1px solid $color-gray-iron;
    }

    &__header-container {
        @include base-container();
        $vertical-padding: toRem(48px);

        display: flex;
        flex-direction: row;
        justify-content: space-between;
        padding-top: $vertical-padding;
        padding-bottom: $vertical-padding;

        @media #{$breakpoint-tablet} {
            $vertical-padding: toRem(24px);

            flex-direction: column-reverse;
            padding-top: $vertical-padding;
            padding-bottom: $vertical-padding;
        }

        &__left,
        &__right {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
        }

        &__right {
            align-items: flex-end;
            justify-content: flex-end;
            max-width: toRem(175px);
            @media #{$breakpoint-tablet} {
                $vertical-padding: toRem(24px);

                max-width: unset;
                flex-direction: row;
                align-items: center;
                justify-content: space-between;
                margin-bottom: $vertical-padding;
                padding-bottom: $vertical-padding;

            }
        }
    }

    &__phone-link {
        display: flex;
        padding: 0.5rem;
        margin-top: 0.5rem;
        align-items: center;
        flex-direction: row;

        .base-image {
            $icon-size: toRem(18px);

            width: $icon-size;
            height: $icon-size;
            margin-right: 0.5rem;
        }
    }

    &__title {
        @media #{$breakpoint-tablet} {
            margin-bottom: 0.5rem;
        }
    }

    &__content {
        @include base-container();
        $vertical-padding: toRem(48px);

        position: relative;
        margin-bottom: toRem(112px);
        padding-top: $vertical-padding;
        padding-bottom: $vertical-padding;



        &-wrapper {
            background-color: $color-gray-light;
            width: 100%;
        }

        &-header {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            margin-bottom: toRem(40px);
            justify-content: flex-start;
        }

        &-title {
            margin-bottom: 0.5rem;
        }

        &-footer {
            display: flex;
            align-items: center;
            flex-direction: column;
            margin-top: toRem(40px);
            justify-content: flex-start;
        }

        &-show-more {
            padding: 0;
            color: $color-gray-corduroy;
        }

        &-promo-code-wrapper {
            display: flex;
            border-radius: 3px;
            height: toRem(48px);
            flex-direction: row;
            align-items: center;
            max-width: toRem(260px);
            background-color: $color-white;
            border: 1px solid $color-gray-light;

            & .base-button {
                background-color: $color-white;
                border-color: transparent;
                position: relative;

                &:before {
                    top: 50%;
                    left: -2px;
                    width: 1px;
                    content: '';
                    position: absolute;
                    height: toRem(24px);
                    transform: translateY(-50%);
                    background-color: $color-gray-iron;
                }
            }
        }

        &-promo-code-label {
            white-space: nowrap;
            padding-right: 0.5rem;
            padding-left: toRem(14px);
            color: $color-gray-corduroy;
        }

        &-promo-code-value {
            overflow: hidden;
            text-overflow: ellipsis;
            padding-right: toRem(14px);
            color: $color-black-woodsmoke;
        }
    }

    &__plans {
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        flex-direction: row;

        @media #{$breakpoint-mobile} {
            align-items: center;
            flex-direction: column;
        }

        @media #{$breakpoint-tablet-and-desktop} {
            .ak-offer:nth-child(3n+2) {
                margin-left: 1.5rem;
                margin-right: 1.5rem;
            }
        }
    }

    &__modal-content {
        width: 100%;
        display: flex;
        flex-direction: row;
        align-items: flex-end;

        .base-input {
            width: 100%;
            margin-right: 0.5rem;
        }

        .base-input__input {
            width: 100%;
            height: toRem(60px);
            max-height: toRem(60px);
        }
    }

    &__modal-cta {
        min-width: unset;
        height: toRem(60px);
        max-width: toRem(130px);

    }

    &__modal-error-msg {
        width: 100%;
        margin-top: 1rem;
        color: $color-red;
        border-radius: 4px;
        text-align: center;
        padding: 0.5rem 1rem;
        background-color: $color-red-light;
    }
}
