
.ak-media-with-text {
    width: 100%;

	&__container {
		padding: toRem(132px) 0;
		@media #{$breakpoint-tablet} {
			display: flex;
			flex-direction: column;
			align-items: center;
			padding: toRem(40px) 0;
		}
	}

    &__text {
		@media #{$breakpoint-desktop-only} {
            max-width: toRem(486px);
		}
	}

	&__subtitleWapper {
		display: flex;
		align-items: center;
		margin-bottom: toRem(16px);
	}

	&__subtitleIcon {
		width: toRem(20px);
		height: toRem(20px);
		margin-left: toRem(10px);
	}

	&__title {
		margin-bottom: toRem(24px);
	}

    &__image {
        width: 100%;

        @media #{$breakpoint-tablet} {
            margin-bottom: 2rem;
        }

		@media #{$breakpoint-desktop-only} {
            max-width: toRem(474px);
            max-height: toRem(498px);
		}
	}

    &__description {
        margin-bottom: toRem(40px);
        word-break: break-word;
	}

	&__button {
		@media #{$breakpoint-tablet} {
			margin-bottom: toRem(40px);
		}
	}

    &__left-margin {
		margin-left: toRem(120px);
		@media #{$breakpoint-tablet} {
            width: 100%;
			margin-left: 0;
		}
    }

    &__right-margin {
		margin-right: toRem(120px);
		@media #{$breakpoint-tablet} {
            width: 100%;
			margin-right: 0;
		}
    }
}
