@import "../../styles/mixins";
@import "../../styles/variables";

.ak-faq-frame {

    &__container {
        padding-top: 1.5rem;
        padding-bottom: 5rem;
        @include base-container();

        @media #{$breakpoint-desktop-only} {
            padding-top: 5.5rem;
        }
    }

    &__content {
        @media #{$breakpoint-desktop-only} {
            margin-left: 23rem;
        }
    }

    &__title {
        margin-bottom: 2rem;
        @media #{$breakpoint-desktop-only} {
            margin-bottom: 3.5rem
        }
    }
}
