@import "../../styles/mixins";
@import "../../styles/variables";

.ak-biography-frame {

    &__container {
        padding-top: 1.5rem;
        margin-bottom: 5rem;
        @include base-container();

        @media #{$breakpoint-desktop-only} {
            padding-top: 6rem;
        }
    }

    &__image-container {
        width: 100%;

        @media #{$breakpoint-tablet-and-desktop} {
            width: 15.75rem;
            min-width: 15.75rem;
        }
    }

    &__image {
        max-height: toRem(437px);

        @media #{$breakpoint-tablet-and-desktop} {
            max-height: none;
			height: toRem(337px);
			object-position: center top;
        }
    }

    &__content {
        width: 100%;
        margin-top: 2.5rem;

        @media #{$breakpoint-tablet-and-desktop} {
            margin-top: 0;
            margin-left: 7.25rem;
        }
    }

    &__job-position {
        color: $color-primary;
    }

    &__details {
        margin-bottom: 3rem;
    }
}
