@import "../../styles/mixins";
@import "../../styles/variables";

.ak-search-page {

	&__container {
		&--empty {
			padding-bottom: toRem(331px);
			@media #{$breakpoint-tablet} {
				padding-bottom: toRem(120px);
			}
		}
	}

	&__top {
		padding-top: toRem(96px);
		padding-bottom: toRem(40px);
		border-bottom: 1px solid $color-gray-iron;

        &--subtitle{
            margin-bottom: 1rem;
            color: $color-black-woodsmoke;
		}

		&--title {
			text-align: center;
			color: $color-blue-klein;
		}
	}

	&__articles-wrapper {
        width: 100%;
        margin: 0 auto;

        @media #{$breakpoint-tablet-and-desktop} {
            margin-top: 2rem;
            max-width: toRem(712px);
        }
	}

    &__article-wrapper {
        padding: toRem(32px) 0;
        border-bottom: 1px solid $color-gray-iron;

        @media #{$breakpoint-tablet} {
            padding: toRem(32px) toRem(24px);
        }

        @media #{$breakpoint-tablet-and-desktop} {
            &:last-child {
                border-bottom: none;
            }
        }
    }

	&__article {
		width: 100%;
        text-align: left;
        color: $color-blue-klein;
	}

    &__excerpt {
        margin-top: toRem(22px);
    }
}
